import React, { useEffect, useRef, useCallback, useState } from "react";
import { useApolloClient } from "@apollo/client";
// import { useHistory } from "react-router-dom";
import { Button, ButtonGrey } from "../../../components/Button";
import Select from "../../../components/Form/Select";
import { useAuth } from "../../../context/AuthContext/AuthProvider";
import { moneyFormatter } from "../../../utils/moneyFormatter";
import {
  OfferStatus,
  LenderOffersDocument,
  LenderOffersQuery,
  FilterOrderType,
  FilterOrder,
} from "../../../types/graphql";
import { SkeletonCard2 } from "../../../components/Skeleton/SkeletonCard2";
// import ErrorCard from "../../../components/ErrorCard";
import { SearchIcon } from "../../../components/Icons/Search";
import Avatar from "../../../components/Avatar";
import { Container } from "./styles";
// import EmptyCard from "../../../components/EmptyCard";
import AvatarDemoImg from "../../../assets/Avatar.svg";

const OFFER_PAGINATION_LIMIT = 10;

const Offers = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [offSet, setOffSet] = useState<number>(0);
  const [offers, setOffers] = useState<LenderOffersQuery["lenderOffers"]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [, setError] = useState<boolean>(false);
  const [offerStatus] = useState<OfferStatus[]>([
    "Pending",
    "Accepted",
    "Settled",
    "NotSettled",
  ] as OfferStatus[]);
  const [sortBy, setSortBy] = useState<FilterOrder>();
  const auth = useAuth();
  const client = useApolloClient();
  // const { push } = useHistory();

  const fetchOffers = async (clearOffset: boolean = false) => {
    let offset = clearOffset ? 0 : offSet;

    try {
      const res: { data: LenderOffersQuery } = await client.query({
        query: LenderOffersDocument,
        variables: {
          id: auth.user.id,
          status: offerStatus,
          term: searchTerm,
          limit: OFFER_PAGINATION_LIMIT,
          offset,
          orderBy: FilterOrderType.CreatedAt,
          order: sortBy,
        },
      });

      if (offset !== 0) {
        let offerData = [...offers, ...res.data.lenderOffers];
        setOffers(offerData);
        setOffSet(offerData.length);
      } else {
        setOffers([...res.data.lenderOffers]);
        setOffSet(res.data.lenderOffers.length);
      }

      setLoading(false);
    } catch (error) {
      console.error("fetchOffers", error);
      setLoading(false);
      setError(true);
    }
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(true);
      fetchOffers(true);
    }, 600);

    return () => clearTimeout(timer);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchTerm, offerStatus]);

  const observer: any = useRef();
  const handlePagination = useCallback(
    (node) => {
      if (observer.current) observer.current.disconnect();
      observer.current = new IntersectionObserver(async (entries) => {
        if (
          entries[0].isIntersecting &&
          offers.length >= OFFER_PAGINATION_LIMIT
        ) {
          await fetchOffers();
        }
      });
      if (node) observer.current.observe(node);
    },
    // eslint-disable-next-line
    [offSet]
  );

  // if (error) {
  //   return (
  //     <ErrorCard
  //       title="Error getting the details"
  //       subTitle={
  //         <>
  //           Possibly a network error.{" "}
  //           <button onClick={() => window.location.reload()}>
  //             Try refreshing
  //           </button>
  //         </>
  //       }
  //     />
  //   );
  // }

  const lenderOffers = offers;

  return (
    <Container>
      {/* Filter Field */}
      <div className="filter">
        <div className="name">
          <div className="filterName">Name</div>
          <div className="filterValue">
            <input
              onChange={(event) => {
                if (lenderOffers.length === 0) setLoading(true);
                setSearchTerm(event.target.value.trim());
              }}
              type="text"
              name="offerName"
              placeholder="Name on offer"
            />
          </div>
        </div>
        <div className="sortBy">
          <div className="filterName">Sort by</div>
          <div className="filterValue">
            <Select
              options={[
                { value: FilterOrder.Desc as string, label: "Most Recent" },
                { value: FilterOrder.Asc as string, label: "Oldest" },
              ]}
              value="Most Recent"
              name="sortBy"
              onChange={(value) => {
                setSortBy(value as FilterOrder);
              }}
              secondaryStyle
            />
          </div>
        </div>
        <button className="searchButton">
          <SearchIcon fontSize="24" color="white" />
        </button>
      </div>

      {/* {!loading && lenderOffers.length === 0 && searchTerm.length > 0 && (
        <EmptyCard
          title={`No offers matching  "${searchTerm}"`}
          subTitle={`Please try different search term`}
          showIllustration={false}
          className="empty-state"
        />
      )}
      {!loading && lenderOffers.length === 0 && searchTerm.length === 0 && (
        <EmptyCard
          title={`No offers`}
          subTitle={`There are no offers.`}
          showIllustration={false}
          className="empty-state"
        />
      )} */}

      {/* List of Loan Requests */}
      <div className="listOfOffers">
        {loading &&
          Array.from(Array(10).keys()).map((_, index) => (
            <SkeletonCard2 key={index} />
          ))}

        {!loading &&
          // lenderOffers?.map((offer, index) => (
          Array.from({ length: 10 }).map((offer, index) => (
            <div key={index} className="offerCard">
              {/* <div key={offer.id} className="offerCard"> */}
              {index === lenderOffers.length - 1 && (
                <div
                  ref={handlePagination}
                  style={{ width: "0%", height: 0 }}
                />
              )}
              <div className="header">
                <Avatar
                  size="large"
                  img={AvatarDemoImg}
                  // img={offer?.borrower?.avatar?.url as string}
                />
                <div className="avatarName">
                  <div className="offerFor">Offer for</div>
                  {/* <div className="offerFor">{offer.status}</div> */}
                  <div className="name">
                    Manny
                    {/* {offer.loanRequest?.loanRequestProfile?.firstName} */}
                  </div>
                </div>
              </div>
              <div className="loanAmount">
                {`${moneyFormatter.format(51954)} - ${moneyFormatter.format(
                  751954
                )}`}
                {/* {moneyFormatter.format(Number(offer.loanAmount))} */}
              </div>
              <div className="loanFrom">
                from{" "}
                <span className="institution">
                  Compenny
                  {/* {offer.lender} */}
                </span>
              </div>
              <div className="content-container">
                <div className="offerDetail">
                  {/* {offer.interestRate} */}
                  Interest rate from
                  <span> 15%</span>
                </div>
                {
                  // offer.comparisonRate &&
                  <div className="offerDetail">
                    {/* {offer.comparisonRate} */}
                    Funding speed
                    <span> 24 hours</span>
                  </div>
                }

                <div className="offerDetail">
                  {/* {offer.loanTerm} */}
                  Maximum loan term
                  <span> 30 months</span>
                </div>
              </div>

              <Button
                className="viewOfferButton"
                // onClick={() => push(`/offer/${offer.id}`)}
              >
                View Offer
              </Button>
              <ButtonGrey
              // onClick={() => push(`/loan-request/${offer.loanRequest.id}`)}
              >
                View Loan Request
              </ButtonGrey>
            </div>
          ))}
      </div>
    </Container>
  );
};

export default Offers;
