import React from "react";
import ProgressBar from "../../../components/ProgressBar";
import StepInfo from "../../../components/Form/StepInfo";
import StepOne from "./StepOne";
import StepTwo from "./StepTwo";
import StepThree from "./StepThree";
import StepFour from "./StepFour";

import { Container } from "./styles";
import {
  State,
  StepOneInputs,
  StepTwoInputs,
  StepThreeInputs,
  StepFourInputs,
  StepFiveInputs,
} from "./types";
import StepFive from "./StepFive";

type Props = {
  state: State;
  onNext: (
    data:
      | StepOneInputs
      | StepTwoInputs
      | StepThreeInputs
      | StepFourInputs
      | StepFiveInputs
  ) => void;
  onBack: () => void;
  toggleModal: (value: boolean) => void;
  completeForm: (data: StepThreeInputs) => void;
  goToStep: (step: number) => void;
};

const LoanRequest = ({
  state,
  onNext,
  onBack,
  toggleModal,
  completeForm,
  goToStep,
}: Props) => {
  const stepInfo = [
    {
      title: "Loan",
      step: 1,
      description: (
        <p>
          Hi there! I'm Samantha, to get started, please provide us with the
          loan details.
        </p>
      ),
    },
    {
      title: "Business Details",
      step: 2,
      description: (
        <p>
          We need to learn a bit about your business now so we can narrow down
          your options!
        </p>
      ),
    },
    {
      title: "Income",
      step: 3,
      description: (
        <p>
          We need to learn more about how your business operates so we can match
          you with relevant loan options.
        </p>
      ),
    },
    {
      title: "Income",
      step: 4,
      description: (
        <p>
          We need to learn more about your income. This is also the final step
          so please{" "}
          <span
            className="link"
            onClick={() => {
              goToStep(1);
            }}
          >
            review
          </span>{" "}
          all your information before submitting.
        </p>
      ),
    },
    {
      title: "Submit",
      step: 5,
      description: (
        <p>
          This is the final step! We only share your information with the
          lenders you choose. If you don't choose a lender your contact details
          will remain hidden.
        </p>
      ),
    },
  ];

  return (
    <Container>
      <div className="inner">
        <ProgressBar progress={(state.step * 100) / 6} />
        <div className="content">
          <div className="left-content">
            <StepInfo
              title={stepInfo[state.step - 1].title}
              step={state.step}
              description={stepInfo[state.step - 1].description}
            />
          </div>
          <div className="right-content">
            {state.step === 1 && (
              <StepOne
                state={state.steps[1]}
                onSubmit={onNext}
                loading={state.loading}
              />
            )}
            {state.step === 2 && (
              <StepTwo
                onBack={onBack}
                state={state.steps[2]}
                onSubmit={onNext}
                loading={state.loading}
              />
            )}
            {state.step === 3 && (
              <StepThree
                onBack={onBack}
                state={state.steps[3]}
                onSubmit={onNext}
                loading={state.loading}
              />
            )}{" "}
            {state.step === 4 && (
              <StepFour
                onBack={onBack}
                state={state.steps[4]}
                onSubmit={onNext}
                loading={state.loading}
              />
            )}{" "}
            {state.step === 5 && (
              <StepFive
                onBack={onBack}
                state={state.steps[5]}
                onSubmit={onNext}
                loading={state.loading}
              />
            )}
          </div>
        </div>
      </div>
    </Container>
  );
};

export default LoanRequest;
