import styled, { css } from "styled-components";

type Props = {
  size: "S" | "M" | "L";
  color: "light" | "dark";
};

export const Container = styled.div<Props>`
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .spinner {
    ${(props) => {
      if (props.size === "S")
        return css`
          width: 20px;
          height: 20px;
        `;
      if (props.size === "M")
        return css`
          width: 40px;
          height: 40px;
        `;
      if (props.size === "L")
        return css`
          width: 50px;
          height: 50px;
        `;
    }}
    position: relative;
    perspective: 1000;
  }

  .double-bounce1,
  .double-bounce2 {
    ${(props) => {
      if (props.size === "S")
        return css`
          width: 20px;
          height: 20px;
        `;
      if (props.size === "M")
        return css`
          width: 40px;
          height: 40px;
        `;
      if (props.size === "L")
        return css`
          width: 50px;
          height: 50px;
        `;
    }}
    border-radius: 50%;
    background-color: ${({ color, theme }) =>
      color === "light" ? theme.primary5 : theme.secondary};
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-animation: sk-bounce 2s infinite ease-in-out;
    animation: sk-bounce 2s infinite ease-in-out;
  }

  .double-bounce2 {
    -webkit-animation-delay: -1s;
    animation-delay: -1s;
  }

  @-webkit-keyframes sk-bounce {
    0%,
    100% {
      -webkit-transform: scale(0);
    }
    50% {
      -webkit-transform: scale(1);
    }
  }

  @keyframes sk-bounce {
    0%,
    100% {
      transform: scale(0);
      -webkit-transform: scale(0);
    }
    50% {
      transform: scale(1);
      -webkit-transform: scale(1);
    }
  }
`;
