import React from "react";
import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import { Container } from "./styles";
import { ButtonDark } from "../../../Button";
import { StyledInput, StyledLabel } from "../../StyledElements";
import FormErrorMessage from "../../../../components/Form/FormErrorMessage";

type Props = {
  onSubmit: (email: string) => void;
  loading: boolean;
};

type Inputs = {
  password: string;
};

const ChangePasswordForm = ({ onSubmit, loading }: Props) => {
  const { register, handleSubmit, errors } = useForm<Inputs>();

  return (
    <Container>
      <div className="mb">
        <StyledLabel htmlFor="password">New Password</StyledLabel>
        <StyledInput
          type="password"
          ref={register({
            required: "This is a required field",
            minLength: {
              message: "Password must be greater than 7 letters",
              value: 8,
            },
          })}
          name="password"
          error={Boolean(errors.password?.message)}
        />
        <FormErrorMessage errors={errors} name="password" />
      </div>
      <div className="mb">
        <ButtonDark
          loading={loading}
          onClick={handleSubmit((data) => {
            onSubmit(data.password);
          })}
        >
          Update Password
        </ButtonDark>
      </div>
      <Link to="/login">
        <p className="link login"> Login</p>
      </Link>
    </Container>
  );
};

export default ChangePasswordForm;
