import React, { useState, useEffect } from "react";
import { Container } from "./styles";
import {
  PhoneNumberInput,
  StyledInput,
  StyledLabel,
  StyledTextArea,
} from "../../../components/Form/StyledElements";
import ImageUpload from "../../../components/ImageUpload";
import { useForm } from "react-hook-form";
import { Button } from "../../../components/Button";
import FormErrorMessage from "../../../components/Form/FormErrorMessage";

import { useAuth } from "../../../context/AuthContext/AuthProvider";
import {
  Avatar,
  File,
  useUpdateProviderMutation,
} from "../../../types/graphql";
import { useAlert } from "../../../context/AlertProvider";
import {
  CognitoUserPool,
  CognitoUserSession,
  ICognitoUserPoolData,
} from "amazon-cognito-identity-js";

type Inputs = {
  profilePicture: string;
  email: string;
  phoneNumber: string;
  password: string;
  startingYear: number;
  info: string;
  fullName: string;
};

const poolData: ICognitoUserPoolData = {
  UserPoolId: process.env.REACT_APP_COGNITO_POOL_ID || "",
  ClientId: process.env.REACT_APP_COGNITO_CLIENT_ID || "",
};

const userPool = new CognitoUserPool(poolData);

const Settings = () => {
  const { user, updateUser } = useAuth();
  const { register, handleSubmit, errors, setValue } = useForm<Inputs>({
    defaultValues: {
      fullName: user.fullName,
      phoneNumber: user.phoneNumber,
      email: user.email,
      info: user.info || "",
      startingYear: user.startingYear || 1992,
    },
  });

  //Custom React Hook Forms Registration For Phone #
  useEffect(() => {
    register(
      { name: "phoneNumber" },
      { required: "This is a required field", minLength: 1 }
    );
  }, [register]);

  const handlePhoneChange = (value: string) => {
    setValue("phoneNumber", value, { shouldValidate: true });
  };

  const [image, setImage] = useState<
    Pick<File, "id" | "name" | "url" | "size"> | undefined
  >();

  const { showAlert } = useAlert();
  const [submitting, setSubmitting] = useState(false);

  const [updateProvider] = useUpdateProviderMutation();

  const uploadImage = async (
    image?: Pick<File, "id" | "name" | "url" | "size"> | undefined
  ) => {
    try {
      await updateProvider({
        variables: {
          avatar: image?.id || user.avatar?.id,
          id: user.id,
        },
      });
      updateUser({
        ...user,
        avatar: (image as Avatar) || user.avatar || undefined,
      });
    } catch (err) {
      console.log(err);
    }
  };

  const onSubmit = async (data: Inputs) => {
    try {
      setSubmitting(true);
      const res = await updateProvider({
        variables: {
          fullName: data.fullName,
          phoneNumber: `${data.phoneNumber}`,
          avatar: image?.id || user.avatar?.id,
          id: user.id,
          info: data.info,
          startingYear: Number(data.startingYear),
          email: data.email,
        },
      });
      const cognitoUser = userPool.getCurrentUser();
      cognitoUser?.getSession((err: any, session: CognitoUserSession) => {
        cognitoUser?.refreshSession(
          session.getRefreshToken(),
          (err, session) => {
            if (err) {
              showAlert({
                show: true,
                description: "Profile update failed!",
                duration: 3000,
                type: "error",
              });
              throw new Error(err);
            }
          }
        );
      });
      showAlert({
        show: true,
        description: "Profile updated",
        duration: 3000,
        type: "success",
      });
      //
      setSubmitting(false);
      updateUser({
        ...user,
        fullName: res.data?.updateProvider.fullName || user.fullName,
        phoneNumber: data.phoneNumber,
        email: res.data?.updateProvider.email || user.email,
        avatar: (image as Avatar) || user.avatar,
        startingYear: data.startingYear,
        info: data.info,
      });
    } catch (err) {
      setSubmitting(false);
      console.log(err);
    }
  };

  return (
    <Container>
      <form className="form">
        <div className="field">
          <StyledLabel htmlFor="profilePicture">Profile Picture</StyledLabel>
          <ImageUpload
            uploadImage={uploadImage}
            setImageType={setImage}
            defaultImage={user.avatar}
          />
          <FormErrorMessage name="" errors={errors} />
        </div>
        <div className="field">
          <StyledLabel htmlFor="loanAmount">Full name</StyledLabel>
          <StyledInput
            name="fullName"
            type="text"
            ref={register({
              required: "This is a required field",
            })}
          />
          <FormErrorMessage errors={errors} name="fullName" />
        </div>
        <div className="field">
          <StyledLabel htmlFor="loanAmount">Email</StyledLabel>
          <StyledInput
            name="email"
            ref={register({
              required: "This is a required field",
              pattern: /\S+@\S+\.\S+/,
            })}
          />
          <FormErrorMessage errors={errors} name="email" />
        </div>
        <div className="field">
          <StyledLabel htmlFor="loanAmount">Phone</StyledLabel>
          <PhoneNumberInput
            error={Boolean(errors.phoneNumber)}
            name="phoneNumber"
            value={user.phoneNumber}
            onChange={(value) => handlePhoneChange(value)}
          />
          <FormErrorMessage name="phoneNumber" errors={errors} />
        </div>
        <div className="field">
          <StyledLabel htmlFor="loanAmount">
            How long have you been a broker
          </StyledLabel>
          <StyledInput
            type="number"
            name="startingYear"
            ref={register({
              required: "This is a required field",
            })}
          />
          <FormErrorMessage name="startingYear" errors={errors} />
        </div>
        <div className="field">
          <StyledLabel htmlFor="loanAmount">Bio</StyledLabel>
          <StyledTextArea
            name="info"
            className="text-area"
            ref={register({
              required: "This is a required field",
            })}
          />
          <FormErrorMessage name="info" errors={errors} />
        </div>
        <Button
          loading={submitting}
          className="submit-btn"
          onClick={handleSubmit(onSubmit)}
        >
          Save Changes
        </Button>
      </form>

      <div className="reviews-container"></div>
    </Container>
  );
};

export default Settings;
