import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
// import LoanRequest from "../screens/Borrower/LoanRequest";
import BusinessLoanRequest from "../screens/Borrower/BusinessLoanRequest";
import Home from "../screens/Borrower/Home";
import Layout from "../components/Layout";
import ReviewBroker from "../screens/Borrower/ReviewBroker";
import Settings from "../screens/Borrower/Settings";
import LoanRequestDetail from "../screens/Borrower/LoanRequestDetail";
import AdditionalQuestions from "../screens/Borrower/AdditionalQuestions";
import OfferDetail from "../screens/Borrower/OfferDetail";
import Logout from "../components/Logout";

const BorrowerRouter = () => {
  return (
    <Router>
      <Layout>
        <Switch>
          <Route exact path="/home" component={Home} />
          <Route exact path="/" render={() => <Redirect to="/home" />} />

          <Route exact path="/loan-request" component={BusinessLoanRequest} />
          <Route
            exact
            path="/loan-request/complete"
            component={BusinessLoanRequest}
          />
          <Route exact path="/settings" component={Settings} />

          {/* // todo: remove */}
          <Route
            path="/review/:loanRequestId/:lenderId/:providerName"
            component={ReviewBroker}
          />
          <Route
            path="/additional-questions/:loanRequestId"
            component={AdditionalQuestions}
          />
          {/* // todo: end remove */}

          <Route exact path="/loan-request/:id" component={LoanRequestDetail} />
          <Route exact path="/offer/:offerId" component={OfferDetail} />

          <Route exact path="/logout" component={Logout} />
          <Redirect to="/" />
        </Switch>
      </Layout>
    </Router>
  );
};

export default BorrowerRouter;
