import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { ContainerS2 as Container } from "./styles";
import {
  StyledInput,
  StyledLabel,
} from "../../../../components/Form/StyledElements";

import FormErrorMessage from "../../../../components/Form/FormErrorMessage";
import MoneyInput from "../../../../components/Form/MoneyInput";
import Select from "../../../../components/Form/Select";
import { Button, ButtonLight } from "../../../../components/Button";
import { StepThreeInputs } from "../types";
import Radio from "../../../../components/Form/Radio";

type Props = {
  state: StepThreeInputs;
  loading: boolean;
  onBack: () => void;
  onSubmit: (data: StepThreeInputs) => void;
};

const StepThree = ({ state, onSubmit, loading, onBack }: Props) => {
  const {
    register,
    unregister,
    handleSubmit,
    errors,
    setValue,
  } = useForm<StepThreeInputs>({
    defaultValues: state,
  });

  const [invoice, setInvoice] = useState("");

  return (
    <Container>
      <div className="form-item">
        <StyledLabel htmlFor="monthlyRevenue">
          What is your monthly revenue?
        </StyledLabel>
        <MoneyInput
          name="monthlyRevenue"
          onInputChange={(value) => {
            setValue("monthlyRevenue", value);
          }}
          defaultValue={state.monthlyRevenue}
          error={Boolean(errors.monthlyRevenue)}
          register={register}
          unregister={unregister}
          validation={{
            required: "This is a required field",
            minLength: 1,
          }}
        />
        <p className="hint">Take an average of your last few months</p>
        <FormErrorMessage name="monthlyRevenue" errors={errors} />
      </div>

      <div className="form-item">
        <StyledLabel>Do you invoice customers?</StyledLabel>
        <Radio
          name="doInvoiceCustomers"
          onChange={(value) => {
            setInvoice(value);
          }}
          value={state.doInvoiceCustomers}
          register={register({
            required: "This is a required field",
            minLength: 1,
          })}
          options={[
            { label: "Yes", value: "yes" },
            { label: "No", value: "no" },
          ]}
        />
        <FormErrorMessage
          className="error_terms"
          errors={errors}
          name="doInvoiceCustomers"
        />{" "}
      </div>
      {invoice === "yes" && (
        <>
          <div className="form-item">
            <StyledLabel>Who do you invoice?</StyledLabel>

            <Select
              options={[
                {
                  value: "customers",
                  label: "Customers",
                },
                {
                  value: "businesses",
                  label: "Businesses",
                },
                {
                  value: "both",
                  label: "Both",
                },
              ]}
              error={Boolean(errors.invoiceClient)}
              value={state.invoiceClient}
              name="invoiceClient"
              selectProps={{
                ref: register({
                  required: "This is a required field",
                  minLength: 1,
                }),
              }}
            />
            <FormErrorMessage name="invoiceClient" errors={errors} />
          </div>

          <div className="form-item">
            <StyledLabel htmlFor="outstandingInvoices">
              What is the value of your outstanding invoices?
            </StyledLabel>
            <StyledInput
              name="outstandingInvoices"
              ref={register({
                required: "This is a required field",
                minLength: 1,
              })}
              error={Boolean(errors.outstandingInvoices)}
            />
            <FormErrorMessage name="outstandingInvoices" errors={errors} />
          </div>
        </>
      )}

      <div className="button-container">
        <ButtonLight onClick={onBack}>
          <img
            src={require("../../../../assets/icons/arrow-back.svg")}
            alt="back"
            className="back-icon"
          />
          Back
        </ButtonLight>

        <Button
          loading={loading}
          onClick={handleSubmit((data) => {
            onSubmit({ ...data });
          })}
          className="submit-button"
        >
          Save and Continue
        </Button>
      </div>
    </Container>
  );
};

export default StepThree;
