export const getStreetName = (street: string) => {
  if (street.toLocaleUpperCase() === "THE ESPLANADE") return "The Esplanade";
  let wordsArr = street.split(" ");
  let lastRemoved = wordsArr.slice(0, wordsArr.length - 1);
  return lastRemoved.join(" ").toLocaleUpperCase();
};

export const getStreetShortName = (street: string) => {
  if (street.toLocaleUpperCase() === "THE ESPLANADE") return "Esplanade";
  let wordsArr = street.split(" ");
  return wordsArr[wordsArr.length - 1].toLocaleUpperCase();
};
