import React from "react";
import { createPortal } from "react-dom";

// import { useOutsideClick } from "../../hooks/useOutsideClick";

import { Container } from "./styles";

import { ArrowIcon } from "../Icons/Arrow";
import { CancelIcon } from "../Icons/Cancel";

type Props = {
  open: boolean;
  toggle: () => void;
};

const Modal = ({ open, toggle, children }: React.PropsWithChildren<Props>) => {
  // const modalBgRef = useOutsideClick(() => toggle());

  return open
    ? createPortal(
        <Container>
          <div className="modal-grid">
            <div className="modal-content">
              <CancelIcon className="cancelIcon" onClick={() => toggle()} />
              <ArrowIcon className="backIcon" onClick={() => toggle()} />
              {children}
            </div>
          </div>
        </Container>,
        document.body
      )
    : null;
};

export default Modal;
