import React from "react";
import styled from "styled-components";

type Props = {
  step: number;
  title: string;
  description: React.ReactNode;
};

const Container = styled.div`
  .title {
    ${({ theme }) => theme.bold_24}
    color: ${({ theme }) => theme.secondary};
    margin-bottom: 16px;

    @media (max-width: ${({ theme }) => theme.breakPoint.SM}) {
      ${({ theme }) => theme.bold_18}
    }
  }
  .step {
    margin-right: 12px;
  }
  .description {
    ${({ theme }) => theme.medium_14}
    color: ${({ theme }) => theme.grey2};
  }
`;

const StepInfo = ({ step, title, description }: Props) => {
  return (
    <Container>
      <h1 className="title">
        <span className="step">0{step}</span>
        {title}
      </h1>
      <p className="description">{description}</p>
    </Container>
  );
};

export default StepInfo;
