import React from "react";
import { Container } from "./styles";
import { StarBoldIcon } from "../Icons/StarBold";
import { StarFilledIcon } from "../Icons/StarFilled";

type Props = {
  onRatingChange?: (value: number) => void;
  ratingValue: number;
  disabled?: boolean;
  small?: boolean;
};

const StarRating = React.memo(
  ({ onRatingChange, ratingValue, disabled, small }: Props) => {
    /**  */

    const onChange = (value: number) => {
      if (onRatingChange) {
        if (value === ratingValue && value !== 1) {
          onRatingChange(value - 1);
        } else {
          onRatingChange(value);
        }
      }
      return null;
    };

    return (
      <Container small={small || false}>
        {[1, 2, 3, 4, 5].map((value, index) => {
          if (ratingValue >= value) {
            return (
              <StarFilledIcon
                key={index}
                onClick={() => !disabled && onChange(value)}
                className="icon star-filled"
              />
            );
          }
          return (
            <StarBoldIcon
              key={index}
              onClick={() => !disabled && onChange(value)}
              className="icon star-bold"
            />
          );
        })}
      </Container>
    );
  }
);

export default StarRating;
