import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import Layout from "../components/Layout";
import Home from "../screens/Lender/Home";
import Offers from "../screens/Lender/Offers";
import Settings from "../screens/Lender/Settings";
import CreateOffer from "../screens/Lender/CreateOffer";
import LoanRequestDetailPage from "../screens/Lender/LoanRequestDetail";
import OfferDetail from "../screens/Lender/OfferDetail";
import Logout from "../components/Logout";

const LenderRouter = () => {
  return (
    <Router>
      <Layout>
        <Switch>
          <Route exact path="/home" component={Home} />
          <Route exact path="/" render={() => <Redirect to="/home" />} />
          <Route exact path="/offers" component={Offers} />
          <Route
            exact
            path="/loan-request/:id"
            component={LoanRequestDetailPage}
          />
          <Route exact path="/settings" component={Settings} />

          <Route exact path="/create-offer" component={CreateOffer} />
          <Route exact path="/edit-offer/:offerId" component={CreateOffer} />

          <Route exact path="/offer/:offerId" component={OfferDetail} />
          <Route exact path="/logout" component={Logout} />
          <Redirect to="/" />
        </Switch>
      </Layout>
    </Router>
  );
};

export default LenderRouter;
