import React, { useEffect } from "react";
import { StyledInput } from "../StyledElements";
import { getStreetName, getStreetShortName } from "./utils";

declare const google: any;

let autocomplete: any;
const scriptId = "google_script";

type AddressDetails = {
  streetNumber: string;
  streetName: string;
  city: string;
  state: string;
  postalCode: string;
  streetType: string;
};

interface IProps extends React.InputHTMLAttributes<HTMLInputElement> {
  // type any custom props you want to pass other than-
  // existing props for a input like "placeholder", "name" or "type"
  error: boolean;
  id: string;
  setError?: (message: string) => void;
  setDetails?: (details: AddressDetails) => void;
}

const AddressInput = (
  {
    error,
    id,
    setError = () => {},
    setDetails = () => {},
    ...inputProps
  }: IProps,
  ref: React.Ref<HTMLInputElement>
) => {
  const initAutocomplete = () => {
    // Create the autocomplete object, restricting the search predictions to
    // geographical location types.
    let autocompleteObj = new google.maps.places.Autocomplete(
      document.getElementById(id),
      { types: ["geocode"] }
    );

    // Avoid paying for data that you don't need by restricting the set of
    // place fields that are returned to just the address components.
    autocompleteObj.setFields(["address_component", "formatted_address"]);

    autocompleteObj.setComponentRestrictions({
      country: ["au"],
    });

    // When the user selects an address from the drop-down, populate the
    // address fields in the form.
    autocomplete = autocompleteObj;
    autocomplete.addListener("place_changed", handlePlaceChange);
  };

  const handlePlaceChange = () => {
    const place = autocomplete.getPlace();
    if (!place) return;
    if (!place.formatted_address) return;
    const filteredFormatted = place.formatted_address
      .replace(", Australia", "")
      .replace("Australia", "");

    if (place.address_components[0].types[0] !== "street_number")
      setError(
        "Please start typing with your street number. It's a required information to pull you credit report."
      );
    else {
      const addressDetails = {
        streetNumber: place.address_components[0].long_name,
        streetName: getStreetName(place.address_components[1].long_name),
        streetType: getStreetShortName(place.address_components[1].short_name),
        city: place.address_components[2].long_name,
        state: place.address_components[4].short_name,
        postalCode: place.address_components[6].long_name,
      };

      setError("");
      setDetails(addressDetails);
    }

    const element = document.getElementById(id);

    (element as HTMLInputElement).value = filteredFormatted as string;
  };

  const createGoogleScript = () => {
    removeGoogleScript();
    const scriptElement = window.document.getElementById(scriptId);
    if (scriptElement) return;
    const googleMapsScript = document.createElement("script");
    const src = `https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_PLACES_API_KEY}&libraries=places`;

    googleMapsScript.src = src;

    googleMapsScript.id = scriptId;

    window.document.body.appendChild(googleMapsScript);

    googleMapsScript.addEventListener("load", initAutocomplete);
  };

  const removeGoogleScript = () => {
    const scriptElement = window.document.getElementById(scriptId);
    const styleElements = Array.from(
      window.document.getElementsByClassName("pac-container pac-logo hdpi")
    );
    styleElements.forEach((item) => {
      window.document.body.removeChild(item as Node);
    });
    if (!scriptElement) return;
    window.document.body.removeChild(scriptElement as Node);
  };

  useEffect(() => {
    createGoogleScript();

    return () => {
      removeGoogleScript();
    };
    // eslint-disable-next-line
  }, []);

  const handleFocus = () => {
    createGoogleScript();
  };

  const handleBlur = () => {
    removeGoogleScript();
  };

  return (
    <div>
      <input style={{ display: "none" }} autoComplete="off" />
      <StyledInput
        {...inputProps}
        error={error}
        ref={ref}
        id={id}
        onFocus={handleFocus}
        onBlur={handleBlur}
      />
    </div>
  );
};

export default React.forwardRef<HTMLInputElement, IProps>(AddressInput);
