const minAge = 20;
const maxAge = 100;

const date = new Date();
const currentYear = date.getFullYear();

export const yearOptions = new Array(maxAge - minAge)
  .fill("")
  .map((_, index) => {
    return {
      label: JSON.stringify(currentYear - minAge - index),
      value: JSON.stringify(currentYear - minAge - index),
    };
  });

export const providerYearOptions = new Array(maxAge - minAge)
  .fill("")
  .map((_, index) => {
    return {
      label: JSON.stringify(currentYear - index),
      value: JSON.stringify(currentYear - index),
    };
  });
