import { css, FlattenSimpleInterpolation } from "styled-components";

type CssSnippets = {
  pulseAnimation: FlattenSimpleInterpolation;
  stickyView: FlattenSimpleInterpolation;
  truncate: FlattenSimpleInterpolation;
  scrollBar: (
    bg: string,
    fg: string,
    width: number
  ) => FlattenSimpleInterpolation;
};

const cssSnippets: CssSnippets = {
  pulseAnimation: css`
    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  `,
  stickyView: css`
    height: 100vh;
    position: sticky;
    top: 0px;
  `,
  truncate: css`
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  `,
  scrollBar: (bg: string, fg: string, width = 8) => css`
    scrollbar-color: ${bg} ${fg};
    scrollbar-width: thin;
    transition: all 0.25s ease-out;

    &::-webkit-scrollbar {
      width: ${width}px;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-track {
      border-radius: 2px;
      background-color: ${fg};
      transition: all 0.25s ease-out;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 2px;
      background-color: ${bg};
      transition: all 0.25s ease-out;
    }
  `,
} as const;

export { cssSnippets };
