import React from "react";
import { useForm } from "react-hook-form";
import { Button } from "../Button";
import FormErrorMessage from "../Form/FormErrorMessage";
import { StyledTextArea } from "../Form/StyledElements";
import { Container } from "./styles";

type Props = {
  question: string;
  answer: string;
  onSubmit: (data: { question: string; answer: string; id: string }) => void;
  id: string;
  loading?: boolean;
};

type Input = {
  answer: string;
};

const AdditionalQuestions = ({
  question,
  answer,
  onSubmit,
  id,
  loading,
}: Props) => {
  const { register, handleSubmit, errors } = useForm<Input>({
    defaultValues: { answer },
  });

  return (
    <Container>
      <div className="inner">
        <h2 className="question">{question}</h2>
        <StyledTextArea
          className="textarea"
          defaultValue={answer}
          name="answer"
          ref={register({ required: "This field is required" })}
        />
        <FormErrorMessage
          className="error-message"
          errors={errors}
          name="answer"
        />
        <Button
          loading={loading}
          onClick={handleSubmit((data) => {
            onSubmit({ question, answer: data.answer, id });
          })}
          className="button"
        >
          {answer ? "Update response" : "Submit"}
        </Button>
      </div>
    </Container>
  );
};

export default AdditionalQuestions;
