import React, { useEffect, useState } from "react";
import {
  LenderOfferAutomationsDocument,
  OfferAutomation,
} from "../../../types/graphql";
import { Button } from "../../../components/Button";
import { moneyFormatter } from "../../../utils/moneyFormatter";
import { SkeletonCard2 } from "../../../components/Skeleton/SkeletonCard2";

import { Container } from "./styles";
import { useApolloClient } from "@apollo/client";
import { useHistory } from "react-router";

const Home = () => {
  const client = useApolloClient();
  const [offerAutomations, setOfferAutos] = useState<[OfferAutomation]>();
  const [loading, setLoading] = useState(true);
  const history = useHistory();

  useEffect(() => {
    fetchOfferAutomations();
    // eslint-disable-next-line
  }, []);

  const fetchOfferAutomations = async () => {
    try {
      const res = await client.query({
        query: LenderOfferAutomationsDocument,
        variables: {
          id: "523f5fe8-f392-4a2a-a6fc-2c3e1df1ec86",
        },
      });
      if (res.data) {
        setOfferAutos(res.data.lenderOfferAutomations);
        setLoading(false);
      }
      console.log("res", res);
    } catch (error) {
      console.log("fetchOfferAutomations", error);
    }
  };

  return (
    <Container>
      <div className="header">
        <h1>Add your loan offers</h1>
        <p>
          Create your loan offers and set the conditions for they are shown.
        </p>
        <Button
          onClick={() => {
            history.push("/create-offer");
          }}
        >
          + Create
        </Button>
      </div>
      <div className="listOfAutomatedOffers">
        {loading && Array.from({ length: 6 }).map(() => <SkeletonCard2 />)}
        {!loading &&
          offerAutomations?.map((item) => (
            <div className="automatedOfferCard">
              <p className="status">Active</p>
              <p className="loanAmount">
                {`${moneyFormatter.format(
                  item.minimumLoanAmount || 0
                )} - ${moneyFormatter.format(item.minimumLoanAmount || 0)}`}
              </p>
              <p className="offerInfo">
                Interest rate from{" "}
                <span className="offerInfoValue">{item.interestRate}%</span>
              </p>
              <p className="offerInfo">
                Funding speed{" "}
                <span className="offerInfoValue">
                  {item.avgFundingSpeed} hours
                </span>
              </p>
              <p className="offerInfo">
                Maximum loan term{" "}
                <span className="offerInfoValue">{item.loanTerm} mon</span>
              </p>
              <Button
                onClick={() => {
                  history.push(`/edit-offer/${item.id}`);
                }}
                className="editAutomationButton"
              >
                Edit Automation
              </Button>
            </div>
          ))}
      </div>
    </Container>
  );
};

export default Home;
