import {
  CognitoUser,
  AuthenticationDetails,
  CognitoUserSession,
} from "amazon-cognito-identity-js";
import { useHistory } from "react-router-dom";
import { useState } from "react";
import { LoginInputs } from "../../screens/Authentication/reducer";
import useUserPool from "./useUserPool";

const useLogin = ({
  onDone,
  onFailure,
}: {
  onDone: (session: CognitoUserSession) => void;
  onFailure?: (error: string) => void;
}) => {
  const [loading, setLoading] = useState(false);
  const userPool = useUserPool();
  const [errorMessage, setErrorMessage] = useState("");
  const history = useHistory();

  const login = (data: LoginInputs) => {
    setLoading(true);
    setErrorMessage("");
    try {
      let authenticationDetails = new AuthenticationDetails({
        Username: data.username.toLocaleLowerCase(),
        Password: data.password,
      });
      let userData = {
        Username: data.username.toLocaleLowerCase(),
        Pool: userPool,
      };

      const cognitoUser = new CognitoUser(userData);
      cognitoUser.authenticateUser(authenticationDetails, {
        onSuccess: async (result) => {
          // here
          onDone(result);
          setLoading(false);
        },
        onFailure: async (err) => {
          // here
          console.error(err);
          let error;
          if (err && err.code === "NetworkError")
            error = "can't connect to the internet";

          if (err && err.code === "PasswordResetRequiredException")
            return history.push("/reset", {
              clueMessageData: "For your security, please reset your password.",
              showClueData: true
            });
          if (onFailure) await onFailure(error as string);
          setErrorMessage("Wrong Email or Password");
          setLoading(false);
        },
      });
    } catch (e) {
      console.error(e);
    }
  };

  return { login, loading, errorMessage };
};
export default useLogin;
