import React from "react";
import { Container, DataItem } from "./styles";
import Card from "../Card";
import { useHistory } from "react-router-dom";
import ToolTip from "../Tooltip";

type Detail = {
  title: string;
  value: string;
  info?: string;
};
type Props = {
  details: Detail[];
  description: string | null | undefined;
  requiredDocs: string | null | undefined;
  loanAmount: number | null | undefined;
  // attachments: Maybe<File>[] | null | undefined;
  lender: string | null;
};

const OfferDetailCard = ({
  details,
  description,
  requiredDocs,
  loanAmount,
  lender,
}: Props) => {
  const { goBack } = useHistory();

  return (
    <Container>
      <Card className="offer-detail" borderBottomHighlight>
        <div className="header-container">
          <div>
            <div className="loan-title">
              <h2>${loanAmount}</h2>
              <p>
                from <span className="lender">{lender} </span>
              </p>
            </div>
          </div>

          <div className="back-container" onClick={() => goBack()}>
            <img
              alt="back"
              src={require("../../assets/icons/arrow-back.svg")}
            />
            <p className="back">Back</p>
          </div>
        </div>
        <div className="content-container">
          <div className="details">
            {details.map((item) => {
              if (
                !(
                  item.value.includes("null") ||
                  item.value.includes("undefined") ||
                  item.value.length === 1
                )
              ) {
                return (
                  <DataItem>
                    <>
                      <div className="data-title">
                        <h3>{item.title}</h3>{" "}
                        {item?.info && (
                          <ToolTip
                            placement="right"
                            content={
                              "Rate includes both the interest rate and the fees and charges relating to a loan, combined into a single percentage figure."
                            }
                          >
                            <img
                              className="info"
                              alt="infos"
                              src={require("../../assets/icons/info-icon.svg")}
                            />
                          </ToolTip>
                        )}
                      </div>
                      <p>{item.value}</p>
                    </>
                  </DataItem>
                );
              } else return null;
            })}
          </div>
          <div className="description">
            <h4>Description</h4>
            <p>{description}</p>
          </div>
          <div className="documents">
            <h4>Required Documents</h4>
            <p>{requiredDocs}</p>
          </div>
          {/* {(attachments?.length as number) > 0 && (
            <div className="attachments">
              <h4>Attachments</h4>

              <div className="attachment-list">
                {attachments?.map((attachment) => (
                  <div className="attachment-item">
                    <a
                      href={`${attachment?.url}`}
                      className="file-link"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <img
                        alt="file"
                        className="file-icon"
                        src={require("../../assets/icons/folder-icon.svg")}
                      />

                      <p className="file-name">{attachment?.name}</p>
                    </a>
                  </div>
                ))}
              </div>
            </div>
          )} */}
        </div>
      </Card>
    </Container>
  );
};

export default OfferDetailCard;
