import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useAlert } from "../../../../context/AlertProvider";
import { User } from "../../../../types/graphql";
import {
  LoanRequestQuery,
  useArchiveLoanRequestMutation,
} from "../../../../types/graphql";
// import { getRelativeTime } from "../../../../utils/getRelativeTime";
import { moneyFormatter } from "../../../../utils/moneyFormatter";
import { Button } from "../../../Button";
import Modal from "../../../Modal";

import { Container, ModalContent } from "./styles";

interface SummaryProps {
  user: User;
  loanRequest: LoanRequestQuery["loanRequest"];
  loanRequestId: string;
}

export const Summary: React.FC<SummaryProps> = ({
  user,
  loanRequestId,
  loanRequest,
}) => {
  const { push } = useHistory();
  const { showAlert } = useAlert();
  const [showModal, setShowModal] = useState(false);
  const toggleModal = () => setShowModal(!showModal);

  const [
    archiveLoanRequest,
    { data: archiveLoanRequestData, error: archiveLoanRequestError },
  ] = useArchiveLoanRequestMutation({
    variables: { id: loanRequestId },
  });

  useEffect(() => {
    if (archiveLoanRequestData?.archiveLoanRequest) {
      showAlert({
        type: "success",
        description: "Loan request has been withdrawn.",
        show: true,
      });
      push("/");
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [archiveLoanRequestData]);

  useEffect(() => {
    if (archiveLoanRequestError) {
      showAlert({
        type: "error",
        description: "Error withdrawing loan request. Try Again.",
        show: true,
      });
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [archiveLoanRequestError]);

  const renderFirstButton = () => {
    if (loanRequest?.status === "Incomplete")
      return (
        <Button
          className="primaryButton"
          onClick={() => {
            push("/loan-request");
          }}
        >
          Complete
        </Button>
      );
  };

  return (
    <>
      <Container>
        <div className="summary">
          <div className="date">
            Posted
            {/* {getRelativeTime(loanRequest?.createdAt)} */}
          </div>
          <div className="loanAmount">
            {moneyFormatter.format(loanRequest?.loanAmount as number)}
          </div>
          <div className="loanType">
            {loanRequest?.loanType?.replace(/([A-Z])/g, " $1").trim()}
          </div>

          {user.role === "borrower" &&
            (loanRequest?.status !== "Archived" ? (
              <>
                {renderFirstButton()}
                <Button
                  className="secondaryButton"
                  onClick={() => toggleModal()}
                >
                  Withdraw Request
                </Button>
              </>
            ) : null)}
          {loanRequest?.status === "Archived" ? (
            <div className="archived">This loan request has been archived.</div>
          ) : null}
        </div>
      </Container>
      <Modal open={showModal} toggle={toggleModal}>
        <ModalContent>
          <div className="modalTitle">Remove loan request</div>
          <div className="modalDescription">
            Are you sure you want to remove this loan request? The request will
            be permanently removed. This action cannot be undone.
          </div>
          <Button
            className="modalRemoveRequest"
            onClick={() => {
              archiveLoanRequest();
              toggleModal();
            }}
          >
            Withdraw Request
          </Button>
        </ModalContent>
      </Modal>
    </>
  );
};
