import React from "react";
import { NavLink } from "react-router-dom";
import { Container } from "./styles";

type Item = {
  to: string;
  title: string;
  external: boolean;
};

type Props = {
  navItems: Item[];
};

const MenuDropdown = ({ navItems }: Props) => {
  return (
    <Container>
      {navItems.map((item) => {
        if (item.title === "Help") {
          return (
            <div className="item-container help">
              <a
                href="https://help.financr.com.au/knowledge"
                target="_blank"
                rel="noopener noreferrer"
                className="item"
              >
                {item.title}
              </a>
            </div>
          );
        } else if (!item.external && item.title !== "Help") {
          return (
            <NavLink
              key={item.title}
              activeClassName="item_active"
              to={item.to}
              className="item-container"
            >
              <div className="item">{item.title}</div>
            </NavLink>
          );
        }

        return null;
      })}
    </Container>
  );
};

export default MenuDropdown;
