import React, { useState, useContext } from "react";

type ConfigType = {
  show: boolean;
  type: "default" | "success" | "error" | "warning";
  duration?: number;
  description?: string;
  redirectLink?: {
    name: string;
    to: string;
  };
};

const initialValues: ConfigType = {
  show: false,
  duration: 3000,
  type: "warning",
  description: "",
  redirectLink: undefined,
};

export const AlertContext = React.createContext({
  ...initialValues,
  showAlert: (config: ConfigType) => {},
  closeAlert: () => {},
});

const AlertProvider: React.FC = ({ children }) => {
  const [alertState, setAlertState] = useState(initialValues);

  const showAlert = (config: ConfigType) => {
    const { type, duration, description, redirectLink } = config;

    setAlertState({
      show: true,
      type,
      description,
      duration,
      redirectLink,
    });

    if (duration !== 0) {
      setTimeout(() => {
        closeAlert();
      }, duration || alertState.duration);
    }
  };

  const closeAlert = () => {
    setAlertState({ ...alertState, show: false });
  };

  return (
    <AlertContext.Provider
      value={{
        ...alertState,
        showAlert: showAlert,
        closeAlert: closeAlert,
      }}
    >
      {children}
    </AlertContext.Provider>
  );
};

export const useAlert = () => useContext(AlertContext);

export default AlertProvider;
