import React from "react";
import { useHistory } from "react-router";
import { LoanRequestQuery } from "../../../../types/graphql";
import { ArrowIcon } from "../../../Icons/Arrow";

import { Container } from "./styles";

interface IncomeProps {
  loanRequest: LoanRequestQuery["loanRequest"];
}

const Details: React.FC<IncomeProps> = ({ loanRequest }) => {
  const history = useHistory();
  const loanDetails = [] as any;
  // const loanDetails = Object.entries(loanRequest?.loanDetail);

  return (
    <Container>
      <div className="header">
        <div className="title">Loan Details</div>
        <button className="backButton" onClick={() => history.goBack()}>
          <ArrowIcon transform="rotate(90)" fontSize="18" />
          <div>Back</div>
        </button>
      </div>
      {loanDetails.map((item: [string, any]) => (
        <div key={item[0]} className="sectionItem">
          <div className="key">
            {item[0]
              ?.replace(/([A-Z])/g, " $1")
              .trim()
              .toLocaleLowerCase()}
          </div>
          <div className="value">
            {item[1]
              ?.replace(/([A-Z])/g, " $1")
              .trim()
              .toLocaleLowerCase()}
          </div>{" "}
        </div>
      ))}
    </Container>
  );
};

export default Details;
