import React, { useState, useEffect } from "react";
import { Container } from "./styles";
import { useOutsideClick } from "../../../hooks/useOutsideClick";

type Option = {
  value: string;
  label: string;
};
type Props = {
  options: Option[];
  selectProps?: object;
  value?: string;
  error?: boolean;
  name?: string;
  onChange?: (value: string) => void;
  secondaryStyle?: boolean;
};

const Select = ({
  options,
  selectProps = {},
  value = "",
  error = false,
  name = "",
  onChange = (value: string) => {},
  secondaryStyle,
}: Props) => {
  const [isOpen, setOpen] = useState(false);
  const [selected, setSelected] = useState(value);

  useEffect(() => {
    const element = document.getElementById(name);
    if (element) {
      (element as HTMLSelectElement).value = selected as string;
    }
    onChange(selected);

    // eslint-disable-next-line
  }, [selected]);

  useEffect(() => {
    setSelected(value);
  }, [value]);

  const selectRef = useOutsideClick(() => setOpen(false));
  return (
    <Container isOpen={isOpen} error={error} secondaryStyle={secondaryStyle}>
      <select {...selectProps} id={name} name={name} className="html-select">
        {options.map((item) => (
          <option key={item.value} value={item.value}>
            {item.label}
          </option>
        ))}
      </select>
      <div
        ref={selectRef}
        onClick={() => {
          setOpen((prev) => !prev);
        }}
        className="custom-select-wrapper"
      >
        <div className={`custom-select ${isOpen && "open"}`}>
          <div className="custom-select__trigger">
            <span className={selected ? "selected-display" : ""}>
              {options.find((item) => item.value === selected)?.label ||
                "Select"}
            </span>
            <div className="arrow"></div>
          </div>
          <div className="custom-options">
            {options.map((item) => (
              <div
                key={item.value}
                onClick={() => {
                  setSelected(item.value);
                }}
                className="option-container"
              >
                <span
                  className={`custom-option ${
                    selected === item.value && "selected"
                  } `}
                  data-value={item.value}
                >
                  {item.label}
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Container>
  );
};

export default Select;
