import React from "react";
import { LoginContainer, LoginFormContainer } from "./styles";
import LoginForm from "../../components/Form/AuthenticationForm/LoginForm";
import useLogin from "../../hooks/authHooks/useLogin";
import { CognitoUserSession } from "amazon-cognito-identity-js";
import { useAuth } from "../../context/AuthContext/AuthProvider";

const Login = () => {
  const { fetchUser } = useAuth();

  const onDone = async (session: CognitoUserSession) => {
    await fetchUser();

    // push("/home");
  };

  const { login, loading, errorMessage } = useLogin({ onDone });

  return (
    <LoginContainer>
      <LoginFormContainer>
        <img
          alt="logo"
          className="logo"
          src={require("../../assets/general/logo.svg")}
        />
        <p className="title">Log in to your account </p>
        <LoginForm
          login={login}
          loading={loading}
          errorMessage={errorMessage}
        />
      </LoginFormContainer>
    </LoginContainer>
  );
};

export default Login;
