import React from "react";
import { useParams } from "react-router-dom";
import { SkeletonContainer } from "../../../components/SharedPageComponents/LoanRequestDetail/styles";
import OfferDetailComponent from "../../../components/SharedPageComponents/OfferDetail";
import { SkeletonCard3 } from "../../../components/Skeleton/SkeletonCard3";
import { SkeletonSection2 } from "../../../components/Skeleton/SkeletonSection2";
import { Offer, useGetOfferQuery } from "../../../types/graphql";
import { Container } from "./styles";

const OfferDetail = () => {
  const params = useParams();

  const { data, loading, refetch } = useGetOfferQuery({
    variables: {
      id: (params as { offerId: string }).offerId,
    },
  });

  return (
    <Container>
      {loading ? (
        <SkeletonContainer>
          <SkeletonCard3 />
          <SkeletonSection2 />
        </SkeletonContainer>
      ) : (
        <OfferDetailComponent offer={data?.offer as Offer} refetch={refetch} />
      )}
    </Container>
  );
};

export default OfferDetail;
