import React, { useState, useEffect, useCallback, useRef } from "react";
import { useApolloClient } from "@apollo/client";
import {
  AffiliateLoanRequestsDocument,
  AffiliateLoanRequestFilterStatus,
  LoanRequest,
} from "../../../types/graphql";
import Select from "../../../components/Form/Select";
import { SearchIcon } from "../../../components/Icons/Search";
import RequestCard from "./RequestCard";
import { Container } from "./styles";
import { SkeletonCard2 } from "../../../components/Skeleton/SkeletonCard2";
import EmptyCard from "../../../components/EmptyCard";
import { useAuth } from "../../../context/AuthContext/AuthProvider";
import { useHistory } from "react-router-dom";

const OFFER_PAGINATION_LIMIT = 10;

const Home = () => {
  const [referralStatus, setReferralStatus] = useState("AllReferrals");
  const [loading, setLoading] = useState(true);
  const [loanRequests, setLoanRequests] = useState<any>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [offSet, setOffset] = useState<number>(0);
  const { user, updateUser } = useAuth();
  const { push } = useHistory();

  useEffect(() => {
    if (user.newUser) {
      push("/new-referral");
      updateUser({ ...user, newUser: false });
      return;
    }
  }, [push, user, updateUser]);

  const client = useApolloClient();

  const fetchLoanRequests = async (clearOffset: boolean = false) => {
    let offset = clearOffset ? 0 : offSet;

    try {
      const res = await client.query({
        query: AffiliateLoanRequestsDocument,
        variables: {
          limit: OFFER_PAGINATION_LIMIT,
          offset,
          status: referralStatus as AffiliateLoanRequestFilterStatus,
          term: searchTerm,
        },
      });

      if (res) {
        if (offset !== 0) {
          const loanRequestData = [
            ...loanRequests,
            ...res.data.affiliateLoanRequests,
          ];
          setLoanRequests(loanRequestData);
          setOffset(loanRequestData.length);
        } else {
          setLoanRequests([...res.data.affiliateLoanRequests]);
          setOffset(res.data.affiliateLoanRequests.length);
        }
      }
      setLoading(false);
    } catch (err) {
      console.error("fetchLoanRequests", err);
    }
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(true);
      fetchLoanRequests(true);
    }, 600);

    return () => clearTimeout(timer);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchTerm, referralStatus]);

  const observer: any = useRef();
  const handlePagination = useCallback(
    (node) => {
      if (observer.current) observer.current.disconnect();
      observer.current = new IntersectionObserver(async (entries) => {
        if (
          entries[0].isIntersecting &&
          loanRequests.length >= OFFER_PAGINATION_LIMIT
        ) {
          await fetchLoanRequests();
        }
      });
      if (node) observer.current.observe(node);
    },
    // eslint-disable-next-line
    [offSet]
  );

  return (
    <Container>
      <div className="search-container">
        <div className="input-field">
          <p className="label">Name</p>
          <input
            onChange={(event) => {
              if (loanRequests.length === 0) setLoading(true);
              setSearchTerm(event.target.value.trim());
            }}
            className="search-input"
            placeholder="Name on enquiry"
          />
        </div>
        <div className="right-content">
          <div className="select">
            <p className="label">Referral status</p>
            <Select
              options={[
                { value: "AllReferrals", label: "All Referrals" },
                { value: "Incomplete", label: "Incomplete" },
                { value: "Completed", label: "Completed" },
                { value: "Selected", label: "Selected" },
                { value: "Settled", label: "Settled" },
                { value: "NotSettled", label: "Not Settled" },
                { value: "Expired", label: "Expired" },
                { value: "BiddingPeriodEnded", label: "Bidding Period Ended" },
              ]}
              value={referralStatus || "AllReferrals"}
              name="referralFilter"
              onChange={(value) => {
                setReferralStatus(value);
              }}
              secondaryStyle
            />
          </div>
          <button className="search-button">
            <SearchIcon fontSize="24" color="white" />
          </button>
        </div>
      </div>

      {!loading && loanRequests.length === 0 && searchTerm.length > 0 && (
        <EmptyCard
          title={`No loan requests matching  "${searchTerm}"`}
          subTitle={`Please try different search term`}
          showIllustration={false}
        />
      )}
      {!loading && loanRequests.length === 0 && searchTerm.length === 0 && (
        <EmptyCard
          title={`No loan requests`}
          subTitle={`There are no loan requests.`}
          showIllustration={false}
        />
      )}
      <div className="content">
        {loading &&
          Array.from(Array(10).keys()).map((_, index) => (
            <SkeletonCard2 key={index} />
          ))}

        {!loading &&
          loanRequests.map((item: LoanRequest, index: number) => {
            return (
              <div>
                {index === loanRequests.length - 1 && (
                  <div
                    ref={handlePagination}
                    style={{ width: "0%", height: 0 }}
                  />
                )}
                <RequestCard loanRequest={item} key={item.id} />
              </div>
            );
          })}
      </div>
    </Container>
  );
};

export default Home;
