import React from "react";
import { useForm } from "react-hook-form";
import { ContainerS4 as Container } from "./styles";
import { StyledLabel } from "../../../../components/Form/StyledElements";
import FormErrorMessage from "../../../../components/Form/FormErrorMessage";
import Radio from "../../../../components/Form/Radio";
import { Button, ButtonLight } from "../../../../components/Button";
import { StepFourInputs } from "../types";
import Select from "../../../../components/Form/Select";

type Props = {
  state: StepFourInputs;
  loading: boolean;
  onSubmit: (data: StepFourInputs) => void;
  onBack: () => void;
};

const StepFour = ({ state, onSubmit, onBack, loading }: Props) => {
  const { register, handleSubmit, errors } = useForm<StepFourInputs>({
    defaultValues: state,
  });

  return (
    <Container>
      <div className="form-item">
        <StyledLabel>How is your credit history?</StyledLabel>
        <Select
          options={[
            {
              value: "allGood",
              label: "All Good",
            },
            {
              value: "fullyPaidDefault",
              label: "A fully paid default",
            },
            {
              value: "unpaidDefault",
              label: "An unpaid default",
            },
          ]}
          error={Boolean(errors.creditHistory)}
          value={state.creditHistory}
          name="creditHistory"
          selectProps={{
            ref: register({
              required: "This is a required field",
              minLength: 1,
            }),
          }}
        />
        <FormErrorMessage name="creditHistory" errors={errors} />
      </div>
      <div className="form-item">
        <StyledLabel htmlFor="ownAProperty">
          Do you own any property?
        </StyledLabel>
        <Radio
          name="ownAProperty"
          register={register({
            required: "This is a required field",
          })}
          value={state.ownAProperty}
          options={[
            { value: "yes", label: "Yes" },
            { value: "no", label: "No" },
          ]}
        />
        <p className="hint">Including any properties currently mortgaged</p>
        <FormErrorMessage name="ownAProperty" errors={errors} />
      </div>

      <div className="button-container">
        <ButtonLight onClick={onBack}>
          <img
            src={require("../../../../assets/icons/arrow-back.svg")}
            alt="back"
            className="back-icon"
          />
          Back
        </ButtonLight>

        <Button
          loading={loading}
          onClick={handleSubmit((data) => {
            onSubmit({ ...data });
          })}
          className="submit-button"
        >
          Save and Continue
        </Button>
      </div>
    </Container>
  );
};

export default StepFour;
