import React from "react";
import { useParams } from "react-router-dom";
import { useLoanRequestQuery } from "../../../types/graphql";
import { useAuth } from "../../../context/AuthContext/AuthProvider";
import { Summary } from "./Summary";
import { SkeletonCard1 } from "../../Skeleton/SkeletonCard1";
import { SkeletonSection1 } from "../../Skeleton/SkeletonSection1";
import ErrorCard from "../../ErrorCard";

import { Container, SkeletonContainer } from "./styles";
import Details from "./Details";

interface LoanRequestDetailProps {
  loanRequestId?: string;
}

type ParamType = {
  id: string;
};

export const LoanRequestDetail: React.FC<LoanRequestDetailProps> = ({
  loanRequestId,
}) => {
  const { user } = useAuth();
  const { id } = useParams<ParamType>();

  const { data, loading, error } = useLoanRequestQuery({
    variables: { id },
  });

  if (error) {
    return (
      <ErrorCard
        title="Error getting the details"
        subTitle={
          <>
            Possibly a network error.{" "}
            <button onClick={() => window.location.reload()}>
              Try refreshing
            </button>
          </>
        }
      />
    );
  }

  if (loading) {
    return (
      <SkeletonContainer>
        <SkeletonCard1 />
        <SkeletonSection1 />
        <SkeletonSection1 />
      </SkeletonContainer>
    );
  }

  const loanRequest = data?.loanRequest;

  return (
    <Container>
      <Summary user={user} loanRequestId={id} loanRequest={loanRequest} />
      <Details loanRequest={loanRequest} />
      {/* {loanRequest?.loanDetail && <Details loanRequest={loanRequest} />} */}
    </Container>
  );
};
