import React, { useEffect, useReducer } from "react";
import { useParams } from "react-router";

// import { useAuth } from "../../../context/AuthContext/AuthProvider";
// import { useAlert } from "../../../context/AlertProvider";
import ProgressBar from "../../../components/ProgressBar";
import LoanRequestFormSkeleton from "../../../components/Skeleton/LoanRequestFromSkeleton";
import StepInfo from "./components/StepInfo";
import StepOne from "./StepOne";
import StepTwo from "./StepTwo";

import { Container } from "./styles";
import { State } from "./types";
import { formReducer } from "./reducer";
import { useApolloClient } from "@apollo/client";
import { OfferAutomationDocument } from "../../../types/graphql";

const initialState: State = {
  step: 1,
  loadingForm: true,
  offerAutomation: {},
  steps: {
    1: {
      productType: "",
      maxLoanAmount: "",
      interestRate: "",
      loanTerm: "",
      avgFundingSpeed: "",
      UpfrontFee: "",
      offerDescription: "",
      requiredDocuments: "",
    },
    2: {
      minimumLoanAmount: "",
      maximumLoanAmount: "",
      minimumGrossSalesPerMonth: "",
      loanPurpose: undefined,
      minimumTimeTrading: undefined,
      property: "",
      creditHistoryAppetite: undefined,
    },
  },
};

type ParamType = {
  offerId: string;
};

const CreateOffer = () => {
  // const auth = useAuth();
  // const alert = useAlert();
  const [state, dispatch] = useReducer(formReducer, initialState);
  const params = useParams<ParamType>();
  const client = useApolloClient();

  const onBack = () => {
    dispatch({ type: "STEP_BACKWARD" });
    window.scrollTo(0, 0);
  };
  const onNext = () => {
    dispatch({ type: "STEP_FORWARD" });
    window.scrollTo(0, 0);
  };

  useEffect(() => {
    if (params.offerId) {
      fetchOfferAutomation(params.offerId);
    }
    // eslint-disable-next-line
  }, []);

  const fetchOfferAutomation = async (offerId: string) => {
    try {
      const res = await client.query({
        query: OfferAutomationDocument,
        variables: {
          id: offerId,
        },
      });
      if (res.data) {
        dispatch({
          type: "SET_OFFER_AUTOMATION",
          payload: res.data.offerAutomation,
        });
        dispatch({
          type: "TOGGLE_FORM_LOADING",
          payload: false,
        });
      }
      console.log("offer", res);
    } catch (error) {
      console.log("error", error);
    }
  };

  // const goToStep = (step: number) => {
  //   // dispatch({ type: "SET_STEP", payload: step });
  // };

  // const showGenericError = () => {
  //   alert.showAlert({
  //     show: true,
  //     type: "error",
  //     description: "Something went wrong. Please try again.",
  //   });
  //   // dispatch({ type: "SET_LOADING", payload: false });
  // };

  if (state.loadingForm) return <LoanRequestFormSkeleton />;

  const stepInfo = [
    {
      title: "Offer",
      step: 1,
      description:
        "This is the offer that get sent when the conditions are met.",
    },
    {
      title: "Conditions",
      step: 2,
      description:
        "Edit the conditions that are required to trigger this automation.",
    },
  ];

  return (
    <Container>
      <ProgressBar progress={100 / (3 - state.step)} />
      <div className="content">
        <div className="leftContent">
          <StepInfo
            title={stepInfo[state.step - 1].title}
            description={stepInfo[state.step - 1].description}
            step={state.step}
            // title={"Offer"}
            // description={
            //   "This is the offer that get sent when the conditions are met. "
            // }
          />
        </div>
        <div className="rightContent">
          {state.step === 1 ? (
            <StepOne onNext={onNext} state={state.offerAutomation} />
          ) : null}
          {state.step === 2 ? <StepTwo onBack={onBack} /> : null}
        </div>
      </div>
    </Container>
  );
};

export default CreateOffer;
