import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import FormErrorMessage from "../../FormErrorMessage";
import {
  LoginInputs,
  RegisterBorrowerInputs,
} from "../../../../screens/Authentication/reducer";
import { useForm } from "react-hook-form";
import { FormContainer, ModalContent } from "./styles";
import { Button, ButtonDark, ButtonOutlined } from "../../../Button";
import {
  StyledInput,
  StyledLabel,
  PhoneNumberInput,
} from "../../StyledElements";
import {
  useCreateBorrowerMutation,
  useSendVerificationMutation,
} from "../../../../types/graphql";
import { useHistory, useParams } from "react-router-dom";
import Modal from "../../../Modal";
import PasswordInput from "../../PasswordInput";

type Props = {
  loading: boolean;
  login: (data: LoginInputs) => void;
};
const BorrowerRegistrationForm = ({ login, loading }: Props) => {
  const { push } = useHistory();
  const { name, email }: { name: string; email: string } = useParams();
  const [verificationModal, setVerificationModal] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [verificationMessage, setVerificationMessage] = useState("");

  const [createBorrower, { loading: creating }] = useCreateBorrowerMutation();

  const [resendCounter, setResendCounter] = useState(10);
  const resendCounterRef = useRef(resendCounter);
  resendCounterRef.current = resendCounter;

  useEffect(() => {
    if (verificationModal) {
      const timer = setInterval(() => {
        if (resendCounterRef.current > 0) {
          setResendCounter((prev) => prev - 1);
        } else clearTimeout(timer);
      }, 1000);
      return () => clearTimeout(timer);
    }
    // eslint-disable-next-line
  }, [verificationModal, resendCounterRef.current]);

  const toggleVerificationModal = () => {
    setVerificationModal((prev) => !prev);
    if (resendCounter < 10) {
      setResendCounter(10);
    }
  };

  const [
    sendVerification,
    { loading: sendingVerification },
  ] = useSendVerificationMutation();

  const handlePhoneChange = (value: string) => {
    setValue("phoneNumber", value, { shouldValidate: true });
  };

  const {
    register,
    handleSubmit,
    errors,
    setValue,
    getValues,
  } = useForm<RegisterBorrowerInputs>({
    defaultValues: {
      name: name?.replace("+", " "),
      email: email,
    },
  });

  useEffect(() => {
    register(
      { name: "phoneNumber" },
      { required: "This is a required field", minLength: 1 }
    );
  }, [register]);

  const onRegisterClick = async (data: RegisterBorrowerInputs) => {
    setVerificationMessage("");
    try {
      await sendVerification({
        variables: {
          email: data.email,
          phoneNumber: data.phoneNumber,
        },
      });
      setVerificationModal(true);
      setErrorMessage("");
    } catch (e) {
      if (e.message.split(":")[0].includes("fetch")) {
        setErrorMessage("check your internet connection and try again");
      } else {
        setErrorMessage(e.message);
      }
    }
  };

  const resend = async ({ email, phoneNumber }: any) => {
    try {
      await sendVerification({
        variables: {
          email,
          phoneNumber,
        },
      });
      setResendCounter(10);
      setVerificationMessage("");
    } catch (e) {
      if (e.message.split(":")[0].includes("fetch")) {
        setVerificationMessage("check your internet connection and try again");
      } else {
        setVerificationMessage(e.message);
      }
      setResendCounter(10);
    }
  };

  function getCookie(cname: string) {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(";");
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) === " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) === 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  const sendDataToHubspot = async ({
    email,
    fullName,
    phoneNumber,
  }: {
    email: string;
    fullName: string;
    phoneNumber: string;
  }) => {
    // get the cookie
    const hutk = getCookie("hubspotutk");
    await axios.post(
      `https://api.hsforms.com/submissions/v3/integration/submit/${process.env.REACT_APP_HUBSPOT_PORTAL_ID}/${process.env.REACT_APP_HUBSPOT_FORM_ID}`,
      {
        fields: [
          {
            name: "email",
            value: email,
          },
          {
            name: "firstname",
            value: fullName.split(" ")[0] ? fullName.split(" ")[0] : "",
          },
          {
            name: "lastname",
            value: fullName.split(" ")[1] ? fullName.split(" ")[1] : "",
          },
          {
            name: "phone",
            value: phoneNumber,
          },
        ],
        context: {
          hutk: hutk,
          pageUri: window.location.host,
          pageName: document.title,
        },
      }
    );
  };

  const onRegister = async (data: RegisterBorrowerInputs) => {
    try {
      if (process.env.NODE_ENV === "production") {
        await sendDataToHubspot({
          email: data.email,
          fullName: data.name,
          phoneNumber: data.phoneNumber,
        });
      }
      await createBorrower({
        variables: {
          input: {
            email: data.email,
            password: data.password,
            phoneNumber: data.phoneNumber,
            fullName: data.name,
            verificationCode: data.verificationCode,
          },
        },
      });
      login({ username: data.email, password: data.password } as LoginInputs);
    } catch (e) {
      if (e.message.split(":")[0].includes("fetch")) {
        setVerificationMessage("check your internet connection and try again");
      } else {
        setVerificationMessage(e.message.split(":")[1]);
      }

      return;
    }
  };

  return (
    <>
      <FormContainer>
        <p className="header-text">Create your account</p>
        <form className="form">
          <div>
            <StyledLabel htmlFor="name">Name</StyledLabel>
            <StyledInput
              name="name"
              ref={register({
                required: "This is a required field",
                minLength: 1,
              })}
              error={Boolean(errors.name)}
            />
            <FormErrorMessage name="name" errors={errors} />
          </div>
          <div>
            <StyledLabel htmlFor="email">Email</StyledLabel>
            <StyledInput
              name="email"
              ref={register({
                required: "This is a required field",
                minLength: 1,
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                  message: "Invalid email address",
                },
              })}
              error={Boolean(errors.email)}
            />
            <FormErrorMessage name="email" errors={errors} />
          </div>
          <div>
            <StyledLabel htmlFor="password">Password</StyledLabel>
            <PasswordInput
              name="password"
              ref={register({
                required: "This is a required field",
                minLength: {
                  value: 8,
                  message: "A minimum of 8 characters must be used",
                },
              })}
              error={Boolean(errors.password)}
            />

            <FormErrorMessage name="password" errors={errors} />
          </div>
          <div>
            <StyledLabel htmlFor="phoneNumber">Phone Number</StyledLabel>
            <PhoneNumberInput
              error={Boolean(errors.phoneNumber)}
              name="phoneNumber"
              value={getValues("phoneNumber")}
              onChange={(value) => handlePhoneChange(value)}
            />
            <FormErrorMessage name="phoneNumber" errors={errors} />
          </div>
          <p className="server-error"> {errorMessage}</p>

          <div className="button-container">
            <ButtonDark
              loading={sendingVerification}
              onClick={handleSubmit(onRegisterClick)}
              className="submit-button"
            >
              Get Started
            </ButtonDark>
          </div>
          <div className="footer">
            <p>
              By clicking "Get Started", I confirm I've read and agree to
              Financr's{" "}
              <a
                href="https://financr.com.au/pages/terms-conditions"
                target="_blank"
                rel="noopener noreferrer"
              >
                {" "}
                <span>Terms of Use </span>{" "}
              </a>{" "}
              and{" "}
              <a
                href="https://financr.com.au/pages/credit-information-privacy-management-policy"
                target="_blank"
                rel="noopener noreferrer"
              >
                {" "}
                <span> Privacy Policy </span>
              </a>
              .
            </p>
            <p>
              Already have an account?{" "}
              <span onClick={() => push("/login")}>Log In </span>
            </p>
          </div>
        </form>
      </FormContainer>
      <Modal open={verificationModal} toggle={() => toggleVerificationModal()}>
        <ModalContent>
          <h2 className="title">Verify phone number</h2>
          <h3 className="subtitle">SMS sent to {getValues("phoneNumber")}</h3>
          <div>
            <StyledLabel htmlFor="verificationCode" className="label">
              Enter Your Confirmation Code
            </StyledLabel>
            <StyledInput
              className="input"
              name="verificationCode"
              ref={register({
                required: "This field is required.",
                minLength: 1,
              })}
              error={Boolean(errors.verificationCode)}
            />
            <FormErrorMessage name="verificationCode" errors={errors} />
          </div>
          <div className="button-container">
            <Button
              className="confirm-btn"
              loading={creating || loading}
              onClick={handleSubmit(onRegister)}
            >
              Confirm
            </Button>
            <ButtonOutlined
              className="back-btn"
              onClick={() => toggleVerificationModal()}
            >
              Back
            </ButtonOutlined>
          </div>
          <p className="server-error"> {verificationMessage} </p>
          {resendCounter === 0 ? (
            <p
              className="resend-active"
              onClick={() =>
                resend({
                  email: getValues("email"),
                  phoneNumber: getValues("phoneNumber"),
                })
              }
            >
              Resend Code
            </p>
          ) : (
            <p className="resend">Resend in {resendCounter}</p>
          )}
        </ModalContent>
      </Modal>
    </>
  );
};

export default BorrowerRegistrationForm;
