import React from "react";
import BorrowerRouter from "./Borrower";
import LenderRouter from "./Lender";
import AffiliateRouter from "./Affiliate";
import AuthRouter from "./Auth";
import { useAuth } from "../context/AuthContext/AuthProvider";
import AlertProvider from "../context/AlertProvider";
import PendingRouter from "./Pending";

const IndexRoute = () => {
  const {
    user: { role, status },
  } = useAuth();

  const renderAppBasedOnRole = () => {
    switch (role) {
      case "borrower":
        return <BorrowerRouter />;
      case "provider":
        return status === "Pending" ? <PendingRouter /> : <LenderRouter />;
      case "affiliate":
        return <AffiliateRouter />;
      default:
        return <AuthRouter />;
    }
  };

  return <AlertProvider>{renderAppBasedOnRole()}</AlertProvider>;
};

export default IndexRoute;
