import React from "react";
import { useForm } from "react-hook-form";
import { Container } from "./styles";
import {
  StyledLabel,
  StyledInput,
} from "../../../../components/Form/StyledElements";
import FormErrorMessage from "../../../../components/Form/FormErrorMessage";
import { Button, ButtonLight } from "../../../../components/Button";
import Select from "../../../../components/Form/Select";
import { StepTwoInputs } from "../types";
import AddressInput from "../../../../components/Form/AddressInput";

type Props = {
  state: StepTwoInputs;
  loading: boolean;
  onSubmit: (data: StepTwoInputs) => void;
  onBack: () => void;
};

const StepTwo = ({ state, onSubmit, onBack, loading }: Props) => {
  const { register, handleSubmit, errors } = useForm<StepTwoInputs>({
    defaultValues: state,
  });

  return (
    <Container>
      <div className="form-item">
        <StyledLabel htmlFor="tradingDuration">
          How long have you been in business?
        </StyledLabel>
        <Select
          options={[
            { value: "lessThan3Months", label: "Under 3 months" },
            { value: "threeTo6Months", label: "3 to 6 months" },
            { value: "sixTo12Months", label: "6 to 12 months" },
            { value: "over12Months", label: "Over 12 months" },
            { value: "oneTo2Years", label: "1 to 2 years" },
            { value: "twoTo5years", label: "2 to 5 years" },
            { value: "over5Years", label: "Over 5 years" },
          ]}
          error={Boolean(errors.tradingDuration)}
          value={state.tradingDuration}
          name={`tradingDuration`}
          selectProps={{
            ref: register({
              required: "This is a required field",
              minLength: 1,
            }),
          }}
        />

        <FormErrorMessage name="tradingDuration" errors={errors} />
      </div>
      <div className="form-item">
        <StyledLabel htmlFor="businessAddress">
          What's your business address?
        </StyledLabel>
        <AddressInput
          id="businessAddress"
          name="businessAddress"
          ref={register({
            required: "This is a required field",
            minLength: 1,
          })}
          error={Boolean(errors.businessAddress)}
        />
        <FormErrorMessage name="businessAddress" errors={errors} />
      </div>
      <div className="form-item">
        <StyledLabel htmlFor="businessAbn">What's your ABN?</StyledLabel>
        <StyledInput
          name="businessAbn"
          ref={register({
            required: "This is a required field",
            minLength: 1,
          })}
          error={Boolean(errors.businessAbn)}
        />
        <p className="hint">
          Not sure? You can look it up{" "}
          <a
            target="_blank"
            className="link"
            href="https://abr.business.gov.au/"
            rel="noopener noreferrer"
          >
            here
          </a>
          . <br /> We should look into ABR API as this will allow us to
          automatically get business create date (relevant for time in business
          question and business name)
        </p>
        <FormErrorMessage name="businessAbn" errors={errors} />
      </div>

      <div className="form-item">
        <StyledLabel htmlFor="industry">What industry are you in?</StyledLabel>
        <Select
          options={[
            { value: "hospitality", label: "Hospitality" },
            {
              value: "financialServicesAndInsurance",
              label: "Financial Services & Insurance",
            },
            { value: "primaryIndustry", label: "Primary Industry" },
            {
              value: "constructionAndTrades",
              label: "Construction and Trades",
            },
            { value: "retail", label: "Retail" },
            { value: "professionalServices", label: "Professional Services" },
            { value: "artsAndLifestyle", label: "Arts & Lifestyle" },
            { value: "manufacturing", label: "Manufacturing" },
            { value: "wholesaleTrade", label: "Wholesale Trade" },
            { value: "health", label: "Health" },
            { value: "hairAndBeauty", label: "Hair & Beauty" },
            { value: "transport", label: "Transport" },
            { value: "other", label: "Other" },
          ]}
          error={Boolean(errors.industry)}
          value={state.industry}
          name={`industry`}
          selectProps={{
            ref: register({
              required: "This is a required field",
              minLength: 1,
            }),
          }}
        />

        <FormErrorMessage name="industry" errors={errors} />
      </div>

      <div className="button-container">
        <ButtonLight onClick={onBack}>
          <img
            src={require("../../../../assets/icons/arrow-back.svg")}
            alt="back"
            className="back-icon"
          />
          Back
        </ButtonLight>

        <Button
          loading={loading}
          onClick={handleSubmit((data) => {
            onSubmit({ ...data });
          })}
          className="submit-button"
        >
          Save and Continue
        </Button>
      </div>
    </Container>
  );
};

export default StepTwo;
