import React from "react";
import PendingCard from "../../components/PendingCard";
import { Container, MobilePendingScreen } from "./style";
import { useMediaQuery } from "react-responsive";
import { NavLink } from "react-router-dom";

const Pending = () => {
  const isTabletOrMobile = useMediaQuery({ query: "(max-width: 560px)" });
  return (
    <Container>
      {isTabletOrMobile ? (
        <MobilePendingScreen>
          <div className="frame">
            <img
              alt="success-icon"
              src={require("../../assets/icons/success-icon.svg")}
            />
            <p className="caption">
              Your application was succesfully submitted. Well be in touch with
              you shortly.
            </p>
            <NavLink to="/logout">
              <p className="logout">Logout</p>
            </NavLink>
          </div>
        </MobilePendingScreen>
      ) : (
        <PendingCard />
      )}
    </Container>
  );
};

export default Pending;
