import React, { useEffect, useState } from "react";
import { Container } from "./styles";
import { StyledLabel } from "../Radio/styles";
import { CheckboxCheckedIcon } from "../../Icons/CheckboxChecked";
import { CheckboxUncheckedIcon } from "../../Icons/CheckboxUnchecked";

type Option = {
  label: string;
  value: string;
};

type Props = {
  inputProps?: object;
  options: Option[];
  values: string[];
  onChange?: (value: string[]) => void;
};

const Checkbox = ({
  inputProps,
  options,
  values,
  onChange = () => {},
}: Props) => {
  const [checked, setChecked] = useState<string[]>(values);

  useEffect(() => {
    onChange(checked);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [checked]);

  const isChecked = (value: string) =>
    checked.some((item: string) => value === item);

  const handleClick = (item: Option) => {
    if (isChecked(item.value)) {
      const filtered = checked.filter((value) => value !== item.value);
      setChecked(filtered);
      return;
    }

    if (!isChecked(item.value)) {
      setChecked((prev: string[]) => [...prev, item.value]);
    }
  };

  return (
    <Container>
      {options.map((item) => (
        <div key={item.value} className="html-checkbox">
          <input
            type="checkbox"
            value={item.value}
            checked={isChecked(item.value)}
            {...inputProps}
          />

          <label>{item.label}</label>
        </div>
      ))}

      {options.map((item) => (
        <div
          key={item.value}
          className="item"
          onClick={() => handleClick(item)}
        >
          {isChecked(item.value) ? (
            <CheckboxCheckedIcon />
          ) : (
            <CheckboxUncheckedIcon />
          )}
          <StyledLabel active={isChecked(item.value)}>{item.label}</StyledLabel>
        </div>
      ))}
    </Container>
  );
};

export default Checkbox;
