import React, { useState } from "react";
import { useLocation, } from "react-router-dom";
import { useSendResetEmailMutation } from "../../types/graphql";
import ResetForm from "../../components/Form/AuthenticationForm/ResetForm";
import { LoginContainer, ResetFormContainer } from "./styles";
import { useAlert } from "../../context/AlertProvider/index";

const Reset = () => {
  const location: any = useLocation();
  const alert = useAlert();
  const [runMutation] = useSendResetEmailMutation();

  const [loading, setLoading] = useState(false);

  const onSubmit = async (email: string) => {
    setLoading(true);
    try {
      const res = await runMutation({ variables: { email: email } });
      if (res.data?.sendResetEmail) {
        alert.showAlert({
          show: true,
          duration: 3000,
          type: "success",
          description: "Please check your email.",
        });
      } else {
        alert.showAlert({
          show: true,
          duration: 3000,
          type: "error",
          description: "Something went wrong. Check if your email is valid.",
        });
      }
      setLoading(false);
    } catch (error) {
      alert.showAlert({
        show: true,
        duration: 3000,
        type: "error",
        description: "Something went wrong.",
      });
      console.error("send reset email", error);
      setLoading(false);
    }
  };


  let clueMessageData;
  let showClueData;
  if (location.state) {
    clueMessageData = location?.state?.clueMessageData;
    showClueData = location.state.showClueData;
  }
  return (
    <LoginContainer>
      <ResetFormContainer>
        {showClueData && <p className="title">{clueMessageData}</p>}
        <img
          alt="logo"
          className="logo"
          src={require("../../assets/general/logo.svg")}
        />
        <p className="title">
          Enter your email and we'll send you a link to reset your password{" "}
        </p>
        <ResetForm onSubmit={onSubmit} loading={loading} />
      </ResetFormContainer>
    </LoginContainer>
  );
};

export default Reset;
