import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import Layout from "../components/Layout";
import Settings from "../screens/Affiliate/Settings";
import Logout from "../components/Logout";
import NewReferral from "../screens/Affiliate/New Referral";
import Home from "../screens/Affiliate/Home";

const AffiliateRouter = () => {
  return (
    <Router>
      <Layout>
        <Switch>
          <Route exact path="/" render={() => <Redirect to="/home" />} />
          <Route exact path="/home" component={Home} />
          <Route exact path="/new-referral" component={NewReferral} />

          <Route exact path="/settings" component={Settings} />
          <Route exact path="/logout" component={Logout} />
          <Redirect to="/" />
        </Switch>
      </Layout>
    </Router>
  );
};

export default AffiliateRouter;
