import React from "react";

import AffiliateRegistrationForm from "../../components/Form/AuthenticationForm/AffiliateRegistrationForm";
import {
  RegistrationContainer,
  SidebarContainer,
  FormContainer,
} from "./styles";
import { CognitoUserSession } from "amazon-cognito-identity-js";
import useLogin from "../../hooks/authHooks/useLogin";
import { useAuth } from "../../context/AuthContext/AuthProvider";
import Testimonial from "../../components/Testimonial";

const AffiliateRegistration = () => {
  const { fetchUser } = useAuth();

  const onDone = async (session: CognitoUserSession) => {
    await fetchUser(true);
  };
  const { login, loading } = useLogin({ onDone });
  return (
    <>
      <RegistrationContainer>
        <SidebarContainer>
          <div className="testimonial-container">
            <Testimonial
              name="Mark Redgewell"
              offer="Weve found Financr to be a great way to meet new clients and help them achieve their goals. Every kind of loan for every kind of customer."
            />
          </div>

          <div className="copy-right-container">
            <p className="copy-right">
              &copy; {new Date().getFullYear()} Financr Pty Ltd 2020 ACN 642 487
              910.
            </p>
          </div>
        </SidebarContainer>

        <FormContainer>
          <img
            alt="logo"
            className="logo"
            src={require("../../assets/general/logo.svg")}
          />
          <AffiliateRegistrationForm login={login} loading={loading} />
        </FormContainer>
      </RegistrationContainer>
    </>
  );
};

export default AffiliateRegistration;
