import React from "react";
import { Link } from "react-router-dom";
import { Container } from "./styles";
import { ButtonDark } from "../../../Button";
import { StyledInput, StyledLabel } from "../../StyledElements";
import { useForm } from "react-hook-form";
import { LoginInputs } from "../../../../screens/Authentication/reducer";
import { useHistory } from "react-router-dom";
import FormErrorMessage from "../../FormErrorMessage";
import PasswordInput from "../../../Form/PasswordInput";

type Props = {
  login: (data: LoginInputs) => void;
  loading: boolean;
  errorMessage?: string;
};
const LoginForm = ({ login, loading, errorMessage }: Props) => {
  const { register, handleSubmit, errors } = useForm({});
  const { push } = useHistory();
  const onSubmit = (data: LoginInputs) => {
    login(data);
  };

  return (
    <Container>
      <div className="input-container">
        <StyledLabel htmlFor="email">Email</StyledLabel>
        <StyledInput
          name="username"
          type="email"
          ref={register({
            required: "This is a required field",
            minLength: 1,
          })}
          error={Boolean(errors.username)}
        />
        <FormErrorMessage name="username" errors={errors} />
      </div>
      <div className="input-container">
        <div className="password-container">
          <StyledLabel htmlFor="password">Password</StyledLabel>
          <Link to="/reset">
            {" "}
            <p className="link forgot-pass">Forgot Password</p>
          </Link>
        </div>
        <PasswordInput
          ref={register({
            required: "This is a required field.",
            minLength: 1,
          })}
          type="password"
          name="password"
          error={Boolean(errors.password)}
        />
        <FormErrorMessage name="password" errors={errors} />
      </div>
      <p className="server-error">{errorMessage} </p>
      <ButtonDark
        loading={loading}
        onClick={handleSubmit((data) => {
          onSubmit(data as LoginInputs);
        })}
      >
        Login
      </ButtonDark>

      <p className="sign-up-text">
        Don't have an account?{" "}
        <span onClick={() => push("/registration/borrower")} className="link">
          Sign Up
        </span>
      </p>
    </Container>
  );
};

export default LoginForm;
