import React from "react";
import OfferDetailCard from "../../OfferDetailCard";
import OfferSideCard from "../../OfferSideCard";
import { Container } from "./styles";
import { Offer, GetOfferQuery, Exact } from "../../../types/graphql";
import { ApolloQueryResult } from "@apollo/client";

interface OfferProps {
  offer: Offer;
  refetch: (
    variables?: Partial<Exact<{ id: string }>> | undefined
  ) => Promise<ApolloQueryResult<GetOfferQuery>>;
}

const OfferDetail: React.FC<OfferProps> = ({ offer, refetch }) => {
  const Details = [
    {
      title: "Interest Rate",
      value: `${offer?.interestRate}%`,
    },
    {
      title: " Term ",
      value: `${offer?.loanTerm} ${
        offer?.loanTerm && offer?.loanTerm > 1 ? "years" : "year"
      }`,
    },
  ];

  return (
    <Container>
      <OfferDetailCard
        details={Details}
        description={offer?.offerDescription}
        requiredDocs={offer?.requiredDocuments}
        // attachments={offer?.attachments}
        loanAmount={offer?.maxOfferLoanAmount}
        lender={offer?.lender?.fullName as string}
      />
      <OfferSideCard />
    </Container>
  );
};

export default OfferDetail;
