import React from "react";
import { useHistory } from "react-router";
import { AllLoanRequest } from "../../../../../types/graphql";

import { Button } from "../../../../../components/Button";
import { WarningIcon } from "../../../../../components/Icons/Warning";
import ToolTip from "../../../../../components/Tooltip";

import { getRelativeTime } from "../../../../../utils/getRelativeTime";
import { moneyFormatter } from "../../../../../utils/moneyFormatter";

import { Container } from "./styles";

type Props = {
  loanRequest: AllLoanRequest;
};

export const LoanRequestCard = ({ loanRequest }: Props) => {
  const history = useHistory();

  return (
    <Container>
      <div className="top">
        <p className="yourRequest">Your Request</p>
        <p className="postedTime">
          Posted {getRelativeTime(loanRequest.updatedAt)}
        </p>
      </div>
      <div className="content">
        <p className="amount">
          {moneyFormatter.format(Number(loanRequest.loanAmount))}
        </p>

        <p className="loanType">
          {/* Business Loan */}
          {loanRequest?.loanType?.replace(/([A-Z])/g, " $1").trim()}
        </p>
      </div>
      <div className="bottom">
        <Button
          className="view-button"
          onClick={() => {
            if (loanRequest.status === "Incomplete")
              return history.push(`/loan-request`);
            history.push(`/loan-request/${loanRequest.id}`);
          }}
        >
          View Loan Request
        </Button>

        {
          //   loanRequestData.status === "Incomplete" &&
          <ToolTip
            placement="right"
            content={
              "Your loan request has not been posted. Please complete your request to receive bids from interested brokers and banks."
            }
          >
            <div className="icon-container" style={{ cursor: "pointer" }}>
              <WarningIcon className="warning-icon" />
            </div>
          </ToolTip>
        }
      </div>
    </Container>
  );
};
