import React, { useEffect, useReducer } from "react";
import { useApolloClient } from "@apollo/client";
import { useHistory } from "react-router-dom";

import LoanRequest from "./LoanRequest";

import { formReducer } from "./reducer";
import {
  StepOneInputs,
  StepTwoInputs,
  StepThreeInputs,
  StepFourInputs,
  StepFiveInputs,
} from "./types";
import {
  BusinessLoanRequest as LoanRequestFullType,
  useCreateBusinessLoanRequestMutation,
  BorrowerLoanRequestDocument,
  useSetBusinessDetailsMutation,
  useSetBusinessIncomeDetailsMutation,
  useSetBusinessBorrowerDetailsMutation,
  useSetBusinessContactDetailsMutation,
  BusinessLoanRequest,
} from "../../../types/graphql";

import { useAuth } from "../../../context/AuthContext/AuthProvider";
import LoanRequestFormSkeleton from "../../../components/Skeleton/LoanRequestFromSkeleton";
import { useAlert } from "../../../context/AlertProvider";
import { getStepData } from "./utils/getStepData";
import { initialState } from "./utils/initialState";

const LoanRequestContainer = () => {
  const auth = useAuth();
  const client = useApolloClient();
  const alert = useAlert();
  const history = useHistory();
  const [state, dispatch] = useReducer(formReducer, initialState);
  // Mutations
  const [createBusinessLoanRequest] = useCreateBusinessLoanRequestMutation();
  const [setBusinessDetailsMutation] = useSetBusinessDetailsMutation();
  const [
    setBusinessIncomeDetailMutation,
  ] = useSetBusinessIncomeDetailsMutation();
  const [setBorrowerDetailsMutation] = useSetBusinessBorrowerDetailsMutation();
  const [setBusinessContactDetails] = useSetBusinessContactDetailsMutation();

  useEffect(() => {
    fetchLoanRequest();
    // eslint-disable-next-line
  }, []);

  const fetchLoanRequest = async () => {
    try {
      const res = await client.query({
        query: BorrowerLoanRequestDocument,
        variables: {
          id: auth.user.id,
        },
      });

      const loanRequest = res.data.borrowerLoanRequest;

      if (isLoanRequestCompleted(loanRequest)) {
        history.push(`/home`);
        return;
      }

      if (isLoanRequestIncomplete(loanRequest)) {
        populateForm(loanRequest);
        return;
      }

      dispatch({ type: "TOGGLE_FORM_LOADING", payload: false });
    } catch (error) {
      console.error("fetchLoanRequest", error);
    }
  };

  const isLoanRequestCompleted = (loanRequest: BusinessLoanRequest) =>
    loanRequest && loanRequest.status === "Completed";
  const isLoanRequestIncomplete = (loanRequest: BusinessLoanRequest) =>
    loanRequest && loanRequest.status === "Incomplete";

  const populateForm = (loanRequest: LoanRequestFullType) => {
    if (loanRequest.step === 1) {
      dispatch({
        type: "POPULATE_STEP_ONE",
        payload: {
          data: getStepData(1, loanRequest) as StepOneInputs,
          id: loanRequest.id,
        },
      });
      dispatch({ type: "SET_LOADING", payload: false });
    } else if (loanRequest.step === 2) {
      dispatch({
        type: "POPULATE_STEP_TWO",
        payload: {
          stepOneData: getStepData(1, loanRequest) as StepOneInputs,
          data: getStepData(2, loanRequest) as StepTwoInputs,
          id: loanRequest.id,
        },
      });
    } else if (loanRequest.step === 3) {
      dispatch({
        type: "POPULATE_STEP_THREE",
        payload: {
          stepOneData: getStepData(1, loanRequest) as StepOneInputs,
          stepTwoData: getStepData(2, loanRequest) as StepTwoInputs,
          data: getStepData(3, loanRequest) as StepThreeInputs,
          id: loanRequest.id,
        },
      });
    } else if (loanRequest.step === 4) {
      dispatch({
        type: "POPULATE_STEP_FOUR",
        payload: {
          stepOneData: getStepData(1, loanRequest) as StepOneInputs,
          stepTwoData: getStepData(2, loanRequest) as StepTwoInputs,
          stepThreeData: getStepData(3, loanRequest) as StepThreeInputs,
          data: getStepData(4, loanRequest) as StepFourInputs,
          id: loanRequest.id,
        },
      });
    }
  };

  const onNext = async (
    data:
      | StepOneInputs
      | StepTwoInputs
      | StepThreeInputs
      | StepFourInputs
      | StepFiveInputs
  ) => {
    dispatch({ type: "SET_LOADING", payload: true });

    if (state.step === 1) {
      createLoanRequest(data as StepOneInputs);
    }
    if (state.step === 2) {
      setLoanBusinessDetails(data as StepTwoInputs);
    }

    if (state.step === 3) {
      setBusinessIncomeDetail(data as StepThreeInputs);
    }
    if (state.step === 4) {
      setBorrowerDetail(data as StepFourInputs);
    }
    if (state.step === 5) {
      setContactDetails(data as StepFiveInputs);
    }
  };

  const createLoanRequest = async (data: StepOneInputs) => {
    try {
      const res = await createBusinessLoanRequest({
        variables: {
          data: {
            borrowerId: auth.user.id,
            loanAmount: parseFloat(data.loanAmount),
            reasonForFunds: data.reasonForFunds,
          },
        },
      });
      if (res.data) {
        dispatch({ type: "STEP_FORWARD", payload: data });
        dispatch({
          type: "SET_LOAN_REQUEST_ID",
          payload: res.data.createBusinessLoanRequest.id,
        });
      }
    } catch (err) {
      console.log("create loan request  ", err);
      showGenericError();
    }
  };

  const setLoanBusinessDetails = async (data: StepTwoInputs) => {
    try {
      const res = await setBusinessDetailsMutation({
        variables: { data, id: state.loanRequestId },
      });
      if (res.data) {
        dispatch({ type: "STEP_FORWARD", payload: data });
      }
    } catch (error) {
      console.log("set loan business details", error);
      showGenericError();
    }
  };

  const setBusinessIncomeDetail = async (data: StepThreeInputs) => {
    try {
      const res = await setBusinessIncomeDetailMutation({
        variables: {
          data: {
            monthlyRevenue: parseFloat(data.monthlyRevenue),
            invoiceClient: data.invoiceClient || "",
            outstandingInvoices: data.outstandingInvoices || "",
          },
          id: state.loanRequestId,
        },
      });

      if (res.data) {
        dispatch({ type: "STEP_FORWARD", payload: data });
      }
    } catch (error) {
      console.log("set business income", error);
      showGenericError();
    }
  };

  const setBorrowerDetail = async (data: StepFourInputs) => {
    try {
      const res = await setBorrowerDetailsMutation({
        variables: {
          data: {
            ownAProperty: data.ownAProperty === "yes" ? true : false,
            creditHistory: data.creditHistory,
          },
          id: state.loanRequestId,
        },
      });
      if (res.data) {
        dispatch({ type: "STEP_FORWARD", payload: data });
      }
    } catch (error) {
      console.log("setBorrowerDetail", error);
      showGenericError();
    }
  };

  const setContactDetails = async (data: StepFiveInputs) => {
    try {
      const res = await setBusinessContactDetails({
        variables: {
          data: {
            companyName: data.companyName,
            firstName: data.firstName,
            lastName: data.lastName,
            phoneNumber: data.phoneNumber,
            tradingName: data.tradingName,
          },
          id: state.loanRequestId,
        },
      });
      if (res.data?.setBusinessContactDetails) {
        history.push("/");
      }
    } catch (error) {
      console.log("setContactDetails", error);
      showGenericError();
    }
  };

  const onBack = () => {
    dispatch({ type: "STEP_BACKWARD" });
  };

  const toggleModal = (value: boolean) => {
    dispatch({ type: "TOGGLE_MODAL", payload: value });
  };

  const goToStep = (step: number) => {
    dispatch({ type: "SET_STEP", payload: step });
  };

  const showGenericError = () => {
    alert.showAlert({
      show: true,
      type: "error",
      description: "Something went wrong. Please try again.",
    });
    dispatch({ type: "SET_LOADING", payload: false });
  };

  if (state.loadingForm) return <LoanRequestFormSkeleton />;

  return (
    <LoanRequest
      state={state}
      onNext={onNext}
      onBack={onBack}
      toggleModal={toggleModal}
      completeForm={() => {
        console.log("complete form");
      }}
      goToStep={goToStep}
    />
  );
};
export default LoanRequestContainer;
