import React, { useState, useContext, useEffect } from "react";
import { User as UserAccount } from "../../types/graphql";
import { UserRole } from "../../types/graphql";
import { CognitoUserSession } from "amazon-cognito-identity-js";
import { useApolloClient } from "@apollo/client";
import useUserPool from "../../hooks/authHooks/useUserPool";
import Spinner from "../../components/Spinner";
import { GET_CURRENT_USER } from "./query";

interface User extends UserAccount {
  newUser?: boolean;
}
const initialValues = {
  fetchUser: (newUser?: boolean) => {},
  checkAuthentication: () => {},
  user: {} as User,
  resetUser: () => {},
  updateUser: (data: User) => {},
};

const AuthContext = React.createContext(initialValues);

const userPool = useUserPool();
const cognitoUser = userPool.getCurrentUser();

const AuthProvider: React.FC = ({ children }) => {
  const client = useApolloClient();
  const [user, setUser] = useState<User>({
    role: "public" as UserRole,
    id: "",
    email: "",
    phoneNumber: "",
    fullName: "",
  });
  const [loading, setLoading] = useState(true);

  const fetchUser = async (newUser?: boolean) => {
    setLoading(true);

    try {
      const { data } = await client.query({
        query: GET_CURRENT_USER,
      });

      setUser({ ...(data.currentUser as User), newUser });
      setLoading(false);
    } catch (err) {
      setLoading(false);
      await cognitoUser?.signOut();
      localStorage.clear();
      console.error(err);
    }
  };
  const checkAuthentication = async () => {
    if (cognitoUser == null) {
      localStorage.clear();
      setLoading(false);
      return;
    }
    cognitoUser.getSession(async (err: any, session: CognitoUserSession) => {
      if (err || !session || !session.isValid()) {
        await cognitoUser.signOut();
        localStorage.clear();
        setLoading(false);
        return;
      }
      await fetchUser();
    });
  };

  const resetUser = async () => {
    setUser({
      role: "public" as UserRole,
      id: "",
      email: "",
      phoneNumber: "",
      fullName: "",
    });
  };

  const updateUser = (data: User) => {
    setUser({ ...data });
  };

  useEffect(() => {
    checkAuthentication();
    // eslint-disable-next-line
  }, []);

  if (loading)
    return (
      <div style={{ height: "100vh" }}>
        <Spinner color="dark" size="L" />
      </div>
    );
  else
    return (
      <AuthContext.Provider
        value={{
          user,
          fetchUser,
          checkAuthentication,
          resetUser,
          updateUser,
        }}
      >
        {children}
      </AuthContext.Provider>
    );
};

export default AuthProvider;

export const useAuth = () => useContext(AuthContext);
