import {
  StepOneInputs,
  StepTwoInputs,
  StepThreeInputs,
  StepFourInputs,
} from "./types";
import { initialState } from "./utils/initialState";

export type Action =
  | {
      type: "STEP_FORWARD";
      payload: StepOneInputs | StepTwoInputs | StepThreeInputs | StepFourInputs;
    }
  | {
      type: "STEP_BACKWARD";
    }
  | {
      type: "SET_LOADING";
      payload: boolean;
    }
  | {
      type: "TOGGLE_FORM_LOADING";
      payload: boolean;
    }
  | {
      type: "TOGGLE_MODAL";
      payload: boolean;
    }
  | {
      type: "POPULATE_STEP_ONE";
      payload: { data: StepOneInputs; id: string };
    }
  | {
      type: "POPULATE_STEP_TWO";
      payload: {
        data: StepTwoInputs;
        stepOneData: StepOneInputs;
        id: string;
      };
    }
  | {
      type: "POPULATE_STEP_THREE";
      payload: {
        data: StepThreeInputs;
        stepOneData: StepOneInputs;
        stepTwoData: StepTwoInputs;
        id: string;
      };
    }
  | {
      type: "POPULATE_STEP_FOUR";
      payload: {
        data: StepFourInputs;
        stepOneData: StepOneInputs;
        stepTwoData: StepTwoInputs;
        stepThreeData: StepThreeInputs;
        id: string;
      };
    }
  | {
      type: "SET_LOAN_REQUEST_ID";
      payload: string;
    }
  | {
      type: "SET_STEP_ONE";
      payload: StepOneInputs;
    }
  | {
      type: "SET_STEP_TWO";
      payload: StepTwoInputs;
    }
  | {
      type: "SET_STEP_THREE";
      payload: StepThreeInputs;
    }
  | {
      type: "SET_STEP";
      payload: number;
    };

export const formReducer = (state = initialState, action: Action) => {
  if (action.type === "STEP_FORWARD") {
    return {
      ...state,
      step: state.step + 1,
      steps: { ...state.steps, [state.step]: action.payload },
      loading: false,
    };
  } else if (action.type === "STEP_BACKWARD") {
    return { ...state, step: state.step - 1 };
  } else if (action.type === "SET_STEP_THREE") {
    return { ...state, steps: { ...state.steps, "3": action.payload } };
  } else if (action.type === "SET_STEP_TWO") {
    return { ...state, steps: { ...state.steps, "2": action.payload } };
  } else if (action.type === "SET_STEP_ONE") {
    return { ...state, steps: { ...state.steps, "1": action.payload } };
  } else if (action.type === "POPULATE_STEP_ONE") {
    return {
      ...state,
      steps: { ...state.steps, "1": action.payload.data },
      loanRequestId: action.payload.id,
      loadingForm: false,
    };
  } else if (action.type === "POPULATE_STEP_TWO") {
    return {
      ...state,
      steps: {
        ...state.steps,
        "1": action.payload.stepOneData,
        "2": action.payload.data,
      },
      loanRequestId: action.payload.id,
      loadingForm: false,
      step: 2,
    };
  } else if (action.type === "POPULATE_STEP_THREE") {
    return {
      ...state,
      steps: {
        ...state.steps,
        "1": action.payload.stepOneData,
        "2": action.payload.stepTwoData,
        "3": action.payload.data,
      },
      loanRequestId: action.payload.id,
      loadingForm: false,
      step: 3,
    };
  } else if (action.type === "POPULATE_STEP_FOUR") {
    return {
      ...state,
      steps: {
        ...state.steps,
        "1": action.payload.stepOneData,
        "2": action.payload.stepTwoData,
        "3": action.payload.stepThreeData,
        "4": action.payload.data,
      },
      loanRequestId: action.payload.id,
      loadingForm: false,
      step: 4,
    };
  } else if (action.type === "SET_LOADING") {
    return { ...state, loading: action.payload };
  } else if (action.type === "TOGGLE_FORM_LOADING") {
    return { ...state, loadingForm: action.payload };
  } else if (action.type === "TOGGLE_MODAL") {
    return { ...state, showModal: action.payload };
  } else if (action.type === "SET_LOAN_REQUEST_ID") {
    return { ...state, loanRequestId: action.payload };
  } else if (action.type === "SET_STEP") {
    return { ...state, step: action.payload };
  }
  return state;
};
