import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Container } from "./styles";
import Logo from "../Icons/Logo";
import { Link, NavLink } from "react-router-dom";
import { ArrowIcon } from "../Icons/Arrow";
import { useMediaQuery } from "react-responsive";
import { MenuIcon } from "../Icons/Menu";
import MenuDropdown from "./MenuDropdown";
import Menu from "./Menu";
import { useOutsideClick } from "../../hooks/useOutsideClick";
import { useAuth } from "../../context/AuthContext/AuthProvider";

type NavItem = {
  to: string;
  title: string;
  external: boolean;
};

const navItems = {
  borrower: [
    { title: "Home", to: "/home", external: false },
    {
      title: "Help",
      to: "https://help.financr.com.au/knowledge",
      external: false,
    },
    { title: "Settings", to: "/settings", external: false },
    { title: "Log Out", to: "/logout", external: false },
  ],
  provider: [
    { title: "Home", to: "/home", external: false },
    {
      to: "/offers",
      title: "Offers",
      external: false,
    },
    {
      to: "https://creditwipeaustralia.com.au/referral-program",
      title: "Credit Repair",
      external: true,
    },
    {
      title: "Help",
      to: "https://help.financr.com.au/knowledge",
      external: false,
    },
    { title: "Settings", to: "/settings", external: false },
    { title: "Log Out", to: "/logout", external: false },
  ],
  affiliate: [
    { title: "Home", to: "/home" },
    {
      to: "/new-referral",
      title: "+ New Referral",
      external: false,
    },
    {
      to: "https://creditwipeaustralia.com.au/referral-program",
      title: "Credit Repair",
      external: true,
    },
    {
      title: "Help",
      to: "https://help.financr.com.au/knowledge",
      external: false,
    },
    { title: "Settings", to: "/settings" },
    { title: "Log Out", to: "/logout" },
  ],
};

const mainNav = {
  provider: [
    {
      to: "/home",
      title: "Home",
      external: false,
    },
    {
      to: "https://creditwipeaustralia.com.au/referral-program",
      title: "Credit Repair",
      external: true,
    },
    {
      to: "/offers",
      title: "Offers",
      external: false,
    },
  ],
  borrower: [
    {
      to: "/home",
      title: "Home",
      external: false,
    },
  ],
  affiliate: [
    {
      to: "/home",
      title: "Home",
      external: false,
    },
    {
      to: "https://creditwipeaustralia.com.au/referral-program",
      title: "Credit Repair",
      external: true,
    },
    {
      to: "/new-referral",
      title: "+ New Referral",
      external: false,
    },
  ],
};

const Navbar = ({ pending }: { pending: boolean | undefined }) => {
  const [showMenu, toggleMenu] = useState(false);
  const [showMobileMenu, toggleMobileMenu] = useState(false);

  const isTabletOrMobile = useMediaQuery({ query: "(max-width: 940px)" });
  const { user } = useAuth();

  const ref = useOutsideClick(() => {
    toggleMenu(false);
  });
  const location = useLocation();

  useEffect(() => {
    toggleMenu(false);
    toggleMobileMenu(false);
  }, [location.pathname]);

  return (
    <Container>
      <Link to="/">
        <Logo />
      </Link>
      <>
        {!pending && (
          <>
            {!isTabletOrMobile && (
              <>
                <div className="nav-items">
                  {(mainNav as any)[user.role || ""].map((item: NavItem) => {
                    if (item.external)
                      return (
                        <React.Fragment key={item.title}>
                          {!((user.role as string) === "borrower") && (
                            <a
                              className="link"
                              target="_blank"
                              rel="noopener noreferrer"
                              href={item.to}
                            >
                              {item.title}
                            </a>
                          )}
                        </React.Fragment>
                      );
                    else {
                      return (
                        <NavLink
                          activeClassName="active-link"
                          className="link"
                          to={item.to}
                          title={item.title}
                          key={item.title}
                        >
                          {item.title}
                        </NavLink>
                      );
                    }
                  })}
                </div>
                <div
                  ref={ref}
                  className="right-content"
                  onClick={() => {
                    toggleMenu((prev) => !prev);
                  }}
                >
                  <img className="avatar" src={user.avatar?.url} alt="avatar" />
                  <p className="name">{user.fullName.slice(0, 16)}</p>

                  <div className="down-arrow-container">
                    <ArrowIcon className="down-arrow" />
                  </div>
                  <div className={`menu-container ${showMenu && "menu-open"}`}>
                    <MenuDropdown
                      navItems={(navItems as any)[user.role || ""].filter(
                        (item: any) => item.to !== "/new-referral"
                      )}
                    />
                  </div>
                </div>
              </>
            )}
            {isTabletOrMobile && (
              <MenuIcon
                onClick={() => {
                  toggleMobileMenu(true);
                }}
                className="menu-icon"
              />
            )}
            {showMobileMenu && (
              <Menu
                navItems={(navItems as any)[user.role || ""]}
                onClose={() => {
                  toggleMobileMenu(false);
                }}
                role={user.role as string}
              />
            )}
          </>
        )}
      </>
    </Container>
  );
};

export default Navbar;
