import React from "react";
import { Container } from "./styles";
import { LoanRequest } from "../../../../types/graphql";
import { moneyFormatter } from "../../../../utils/moneyFormatter";
import { truncate } from "../../../../utils/helpers";
import { getRelativeTime } from "../../../../utils/getRelativeTime";

type Props = {
  loanRequest: LoanRequest;
};

const RequestCard = ({ loanRequest }: Props) => {
  return (
    <Container>
      <div className="request-card">
        <div className="top">
          <img
            className="avatar"
            alt="avatar"
            src={loanRequest.borrower?.avatar?.url}
          />
          <div>
            <p className="status">
              {" "}
              {loanRequest?.affiliateStatus?.replace(/([A-Z])/g, " $1").trim()}
            </p>
            <p className="name">
              {truncate(loanRequest.loanRequestProfile?.firstName || "", 16)}
            </p>
          </div>
        </div>
        <p className="amount">
          {loanRequest.loanAmount &&
            moneyFormatter.format(Number(loanRequest.loanAmount))}
        </p>
        <p className="regular-text">{loanRequest.borrower?.email}</p>
        <p className="regular-text">
          {loanRequest.loanType?.replace(/([A-Z])/g, " $1").trim()}
        </p>
        <p className="regular-text">
          Posted {getRelativeTime(loanRequest.createdAt)}
        </p>
      </div>
    </Container>
  );
};

export default RequestCard;
