import React, { useState } from "react";
import { useParams } from "react-router-dom";
import {
  useGetLoanRequestQuestionsQuery,
  useUpdateLoanRequestQuestionMutation,
} from "../../../types/graphql";
import { Container, CardContainer } from "./styles";
import AnswerFromCard from "../../../components/AnswerFromCard";
import { useAlert } from "../../../context/AlertProvider";
import AnswerFromCardSkeleton from "../../../components/Skeleton/AnswerFromCardSkeleton";

const AdditionalQuestions = () => {
  const [loadingId, setLoading] = useState("");
  const params = useParams();
  const alert = useAlert();

  const { data, refetch, loading } = useGetLoanRequestQuestionsQuery({
    variables: {
      loanRequestId: (params as { loanRequestId: string }).loanRequestId,
    },
  });

  const [updateQuestion] = useUpdateLoanRequestQuestionMutation();

  const onSubmit = async (data: {
    question: string;
    answer: string;
    id: string;
  }) => {
    setLoading(data.id);
    try {
      const res = await updateQuestion({
        variables: { answer: data.answer, id: data.id },
      });
      if (res) {
        refetch();
        alert.showAlert({
          show: true,
          type: "success",
          description: "Response submitted successfully!",
        });
        setLoading("");
      }
    } catch (error) {}
  };

  return (
    <Container>
      <div className="inner-screen">
        {loading &&
          [1, 2, 3, 4].map((item) => (
            <div className="skeleton-card-container" key={item}>
              <AnswerFromCardSkeleton />
            </div>
          ))}
        {data?.loanRequestQuestions
          .sort((a, b) => {
            if (a?.answer) return 1;
            else return -1;
          })
          .map(
            (item) =>
              item && (
                <CardContainer visible={true} className="card-container">
                  <AnswerFromCard
                    question={item.question || ""}
                    key={item?.id}
                    answer={item?.answer || ""}
                    id={item?.id}
                    onSubmit={onSubmit}
                    loading={loadingId === item.id}
                  />
                </CardContainer>
              )
          )}{" "}
      </div>
    </Container>
  );
};

export default AdditionalQuestions;
