import { BusinessLoanRequest } from "../../../../types/graphql";

export const getStepData = (step: number, loanRequest: BusinessLoanRequest) => {
  if (step === 1)
    return {
      loanAmount: JSON.stringify(loanRequest.loanAmount),
      reasonForFunds: loanRequest.reasonForFunds || "",
    };

  if (step === 2)
    return {
      industry: loanRequest.industry || "",
      tradingDuration: loanRequest.tradingDuration || "",
      businessAddress: loanRequest.businessAddress || "",
      businessAbn: loanRequest.businessAbn || "",
    };
  if (step === 3) {
    return {
      monthlyRevenue: JSON.stringify(loanRequest.monthlyRevenue) || "",
      invoiceClient: loanRequest.invoiceClient || null,
      outstandingInvoices: loanRequest.outstandingInvoices || null,
      doInvoiceCustomers: loanRequest.invoiceClient ? "yes" : "no",
    };
  }

  if (step === 4) {
    return {
      ownAProperty: loanRequest.ownAProperty ? "yes" : "no",
      creditHistory: loanRequest.creditHistory,
    };
  }
};
