import React, { useState, useEffect } from "react";
import { Container } from "./styles";
import {
  StyledInput,
  StyledLabel,
  PhoneNumberInput,
} from "../../../components/Form/StyledElements";
import ImageUpload from "../../../components/ImageUpload";
import { useForm } from "react-hook-form";
import { Button } from "../../../components/Button";
import FormErrorMessage from "../../../components/Form/FormErrorMessage";
import { useAuth } from "../../../context/AuthContext/AuthProvider";
import {
  Avatar,
  File,
  useUpdateAffiliateMutation,
} from "../../../types/graphql";
import { useAlert } from "../../../context/AlertProvider";
import {
  CognitoUserPool,
  CognitoUserSession,
  ICognitoUserPoolData,
} from "amazon-cognito-identity-js";

type Inputs = {
  profilePicture: string;
  email: string;
  phoneNumber: string;
  accountNumber: number;
  accountName: string;
  bsb: string;
};

const poolData: ICognitoUserPoolData = {
  UserPoolId: process.env.REACT_APP_COGNITO_POOL_ID || "",
  ClientId: process.env.REACT_APP_COGNITO_CLIENT_ID || "",
};

const userPool = new CognitoUserPool(poolData);

const Settings = () => {
  const { user, updateUser } = useAuth();
  const { register, handleSubmit, errors, setValue } = useForm<Inputs>({
    defaultValues: {
      phoneNumber: user.phoneNumber,
      email: user.email,
      accountName: user.fullName,
      bsb: user.bsb || "",
      accountNumber: user.accountNumber || 0,
    },
  });

  const [
    updateAffiliate,
    { loading: submitting },
  ] = useUpdateAffiliateMutation();
  const { showAlert } = useAlert();
  const [image, setImage] = useState<
    Pick<File, "id" | "name" | "url" | "size"> | undefined
  >();
  const [uploadingImage, setIsUploading] = useState(false);

  useEffect(() => {
    register(
      { name: "phoneNumber" },
      { required: "This is a required field", minLength: 1 }
    );
  }, [register]);

  const handlePhoneChange = (value: string) => {
    setValue("phoneNumber", value, { shouldValidate: true });
  };

  const uploadImage = async (
    image?: Pick<File, "id" | "name" | "url" | "size"> | undefined
  ) => {
    try {
      await updateAffiliate({
        variables: {
          avatar: image?.id || user.avatar?.id,
          id: user.id,
        },
      });
      updateUser({
        ...user,
        avatar: (image as Avatar) || user.avatar || undefined,
      });
    } catch (err) {
      console.log(err);
    }
  };

  const onSubmit = async (data: Inputs) => {
    try {
      const res = await updateAffiliate({
        variables: {
          phoneNumber: `${data.phoneNumber}`,
          id: user.id,
          avatar: image?.id || user.avatar?.id,
          accountNumber: Number(data.accountNumber),
          fullName: data.accountName || user.fullName,
          bsb: data.bsb,
          email: data.email,
        },
      });
      if (res.data) {
        const cognitoUser = userPool.getCurrentUser();
        cognitoUser?.getSession((err: any, session: CognitoUserSession) => {
          cognitoUser?.refreshSession(
            session.getRefreshToken(),
            (err, session) => {
              if (err) {
                showAlert({
                  show: true,
                  description: "Profile update failed!",
                  duration: 3000,
                  type: "error",
                });
                throw new Error(err);
              }
            }
          );
        });
        showAlert({
          show: true,
          description: "Profile updated",
          duration: 3000,
          type: "success",
        });
        updateUser({
          ...user,
          fullName: data.accountName,
          phoneNumber: data.phoneNumber,
          email: data.email,
          avatar: (image as Avatar) || user.avatar || undefined,
          accountNumber: Number(data.accountNumber),
          bsb: data.bsb,
        });
      }
    } catch (err) {
      console.error(err);
      showAlert({
        show: true,
        description: "Profile update failed!",
        duration: 3000,
        type: "error",
      });
    }
  };

  return (
    <Container>
      <form className="form">
        <div className="field">
          <StyledLabel htmlFor="loanAmount">Profile Picture</StyledLabel>
          <ImageUpload
            uploadImage={uploadImage}
            defaultImage={user.avatar}
            setImageType={setImage}
            uploadingImage={setIsUploading}
          />
        </div>
        <div className="field">
          <StyledLabel htmlFor="loanAmount">Email</StyledLabel>
          <StyledInput
            name="email"
            type="email"
            ref={register({
              required: "This is a required field",
              pattern: /\S+@\S+\.\S+/,
            })}
          />
          <FormErrorMessage errors={errors} name="email" />
        </div>
        <div>
          <StyledLabel htmlFor="phoneNumber">Phone Number</StyledLabel>
          <PhoneNumberInput
            error={Boolean(errors.phoneNumber)}
            name="phoneNumber"
            value={user.phoneNumber}
            onChange={(value) => handlePhoneChange(value)}
          />
          <FormErrorMessage name="phoneNumber" errors={errors} />
        </div>
        <div className="field">
          <StyledLabel htmlFor="accountNumber">Account Number</StyledLabel>
          <StyledInput
            name="accountNumber"
            type="number"
            ref={register({
              required: "This is a required field",
            })}
          />
          <FormErrorMessage errors={errors} name="accountNumber" />
        </div>
        <div className="field">
          <StyledLabel htmlFor="accountName">Account Name</StyledLabel>
          <StyledInput
            name="accountName"
            type="text"
            ref={register({
              required: "This is a required field",
            })}
          />
          <FormErrorMessage errors={errors} name="accountName" />
        </div>
        <div className="field">
          <StyledLabel htmlFor="bsb">BSB</StyledLabel>
          <StyledInput
            name="bsb"
            type="text"
            ref={register({
              required: "This is a required field",
            })}
          />
          <FormErrorMessage errors={errors} name="bsb" />
        </div>
        <Button
          loading={submitting || uploadingImage}
          className="submit-btn"
          onClick={handleSubmit(onSubmit)}
        >
          Save Changes
        </Button>
      </form>
    </Container>
  );
};

export default Settings;
