import React, { useState, useEffect } from "react";
import { Container, StyledLabel } from "./styles";
import { RadioCheckedIcon } from "../../Icons/RadioChecked";
import { RadioUncheckedIcon } from "../../Icons/RadioUnchecked";

type Option = {
  value: string;
  label: string;
};

type Props = {
  options: Option[];
  value?: string;
  onChange?: (value: string) => void;
  name?: string;
  register?: any;
};

const Radio = ({
  options,
  value = "",
  onChange = () => {},
  name,
  register,
}: Props) => {
  const [radioValue, setValue] = useState(value);

  useEffect(() => {
    onChange(radioValue);
    // eslint-disable-next-line
  }, [radioValue]);

  useEffect(() => {
    setValue(value);
  }, [value]);

  return (
    <Container>
      {options.map((item) => (
        <div
          key={item.value}
          className="item"
          onClick={() => {
            setValue(item.value);
          }}
        >
          <input
            type="radio"
            value={item.value}
            name={name}
            className="htmlRadio"
            onChange={(e) => {
              setValue(e.target.value);
            }}
            checked={item.value === radioValue}
            ref={register}
          />
          {item.value === radioValue ? (
            <RadioCheckedIcon />
          ) : (
            <RadioUncheckedIcon />
          )}
          <StyledLabel active={item.value === radioValue}>
            {item.label}
          </StyledLabel>
        </div>
      ))}
    </Container>
  );
};

export default Radio;
