import React from "react";
import { NavLink } from "react-router-dom";
import { Container } from "./styles";
import { CancelIcon } from "../../Icons/Cancel";

type Item = {
  to: string;
  title: string;
  external: boolean;
};

type Props = {
  navItems: Item[];
  onClose: () => void;
  role: string;
};

const Menu = ({ navItems, onClose, role }: Props) => {
  return (
    <Container>
      <div onClick={onClose}>
        <CancelIcon className="close-icon" width={28} height={28} />
      </div>
      <div>
        {navItems.map((item) => {
          if (item.external) {
            return (
              <>
                {role !== "borrower" && (
                  <a
                    target="_blank"
                    className="item-container"
                    rel="noopener noreferrer"
                    href={item.to}
                  >
                    <div className="item">{item.title}</div>
                  </a>
                )}
              </>
            );
          } else if (item.title === "Help") {
            return (
              <div className="item-container help">
                <a
                  href="https://help.financr.com.au/knowledge"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="item"
                >
                  {item.title}
                </a>
              </div>
            );
          } else {
            return (
              <NavLink
                activeClassName="item_active"
                to={item.to}
                className="item-container"
              >
                <div className="item">{item.title}</div>
              </NavLink>
            );
          }
        })}
      </div>
    </Container>
  );
};

export default Menu;
