import React from "react";
import { Container } from "./styles";

type Props = {
  className?: string;
  borderBottomHighlight?: boolean;
};

const Card: React.FC<Props> = ({
  children,
  className,
  borderBottomHighlight,
}) => {
  return (
    <Container
      borderBottomHighlight={borderBottomHighlight}
      className={className}
    >
      {children}
    </Container>
  );
};

export default Card;
