import styled, { css } from "styled-components";
import React, { useState } from "react";
import { useOnFocus } from "../../../hooks/useOnFocus";
import PhoneInput from "react-phone-number-input";
import "react-phone-number-input/style.css";

type InputProps = {
  error?: boolean;
};

type InputContainerProps = {
  active: boolean;
  error?: boolean;
};

type FileInputProps = {
  active?: boolean;
  error?: boolean;
};
export const baseInput = css<InputProps>`
  padding: 7px 32px 7px 12px;
  border-radius: 8px;
  width: 100%;
  color: ${(props) => props.theme.grey1};
  ${(props) => props.theme.medium_14};
  ${(props) => {
    if (props.error)
      return css`
        border: solid ${props.theme.error} 1px;
      `;
    else
      return css`
        border: solid ${props.theme.grey6} 1px;
        &:focus {
          border: solid ${(props) => props.theme.primary1} 1px;
        }
      `;
  }}
  outline: none;
  -webkit-appearance: none;
`;

export const StyledLabel = styled.label`
  ${(props) => props.theme.medium_14};
  color: ${(props) => props.theme.secondary};
  margin-bottom: 7px;
  display: inline-block;
`;

export const StyledInput = styled.input<InputProps>`
  ${baseInput}
`;
export const StyledTextArea = styled.textarea<InputProps>`
  ${baseInput}
  resize: none;
`;

/* CUSTOM INPUTS WITH DEFAULT TEXT AT THE BEGINNING */

export const StyledPhoneInput = styled.div<InputContainerProps>`
  ${baseInput};

  padding: 0;
  display: flex;
  align-items: center;
  background: white;
  justify-content: center;

  .PhoneInput {
    width: 100%;
    margin-left: 12px;
  }
  .PhoneInputCountryIcon {
    margin-right: 4px;
  }
  input {
    border: none;

    width: 100%;
    height: 36.8px;
    padding: 7.8px 32px 7px 7.8px;
    color: ${(props) => props.theme.grey1};
    ${(props) => props.theme.medium_14};
    border-bottom-right-radius: 8px;
    border-top-right-radius: 8px;
    :focus {
      border: none;
    }
  }
  ${(props) => {
    if (props.error)
      return css`
        border: solid ${props.theme.error} 1px;
      `;
    else
      return css`
        border: solid ${props.active ? props.theme.primary1 : props.theme.grey6}
          1px;
      `;
  }}
`;

/* LOAN AMOUNT INPUT */
export const StyledDollarInput = styled.div<InputContainerProps>`
  ${baseInput};

  display: flex;
  align-items: center;
  background: white;
  ::before {
    content: "$";
    padding-left: 12px;
  }
  input {
    border: none;
    padding: 7px 32px 7px 2px;

    :focus {
      border: none;
    }
  }
  ${(props) => {
    if (props.error)
      return css`
        border: solid ${props.theme.error} 1px;
      `;
    else
      return css`
        border: solid ${props.active ? props.theme.primary1 : props.theme.grey6}
          1px;
      `;
  }}
`;

type CustomInputProps = {
  error: boolean;
  name: string;
  inputRef?: (ref: HTMLInputElement) => void;
  value?: string;
  onChange?: (e: any) => void;
};
type PhoneInputProps = {
  error: boolean;
  name: string;
  value?: string;
  onChange: (e: any) => void;
};
export const DollarInput = ({ error, name, inputRef }: CustomInputProps) => {
  const [active, setActive] = useState(false);
  const ref = useOnFocus(
    () => setActive(false),
    () => setActive(true)
  );
  return (
    <StyledDollarInput error={error} active={active} ref={ref as any}>
      <StyledInput ref={inputRef} name={name} />
    </StyledDollarInput>
  );
};

export const PhoneNumberInput = ({
  value,
  error,
  name,
  onChange,
}: PhoneInputProps) => {
  const [active, setActive] = useState(false);
  const ref = useOnFocus(
    () => setActive(false),
    () => setActive(true)
  );

  return (
    <StyledPhoneInput error={error} active={active} ref={ref as any}>
      <PhoneInput
        value={value as string}
        name={name}
        onChange={(value: string) => onChange(value)}
        defaultCountry="AU"
        placeholder=""
      />
    </StyledPhoneInput>
  );
};
