import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import Alert from "../components/Alert";
import {
  BorrowerRegistration,
  Login,
  Reset,
  ChangePassword,
  ProviderRegistration,
  AffiliateRegistration,
} from "../screens/Authentication";

const AuthRouter = () => {
  return (
    <Router>
      <Alert />
      <Switch>
        <Route
          exact
          path="/"
          render={() => <Redirect to="/registration/borrower" />}
        />
        <Route
          exact
          path="/registration"
          render={() => <Redirect to="/registration/borrower" />}
        />
        <Route
          path="/registration/borrower/:email/:name"
          component={BorrowerRegistration}
        />{" "}
        <Route path="/registration/borrower" component={BorrowerRegistration} />
        <Route
          exact
          path="/registration/provider"
          component={ProviderRegistration}
        />
        <Route
          exact
          path="/registration/affiliate"
          component={AffiliateRegistration}
        />
        <Route path="/login" component={Login} />
        <Route exact path="/reset" component={Reset} />
        <Route
          exact
          path="/change-password/:token"
          component={ChangePassword}
        />
        <Redirect to="/login" />
      </Switch>
    </Router>
  );
};

export default AuthRouter;
