import React from "react";
import { useForm } from "react-hook-form";
import { Container } from "./styles";
import { StyledLabel } from "../../../../components/Form/StyledElements";
import FormErrorMessage from "../../../../components/Form/FormErrorMessage";
import Select from "../../../../components/Form/Select";
import { Button } from "../../../../components/Button";
import MoneyInput from "../../../../components/Form/MoneyInput";
import { StepOneInputs } from "../types";

type Props = {
  state: StepOneInputs;
  loading: boolean;
  onSubmit: (data: StepOneInputs) => void;
};

const StepOne = ({ state, onSubmit, loading }: Props) => {
  const {
    register,
    unregister,
    handleSubmit,
    errors,
    setValue,
  } = useForm<StepOneInputs>({
    defaultValues: state,
  });

  return (
    <Container>
      <div className="form-item">
        <StyledLabel htmlFor="loanAmount">
          How much are you looking to borrow?
        </StyledLabel>

        <MoneyInput
          name="loanAmount"
          onInputChange={(value) => {
            setValue("loanAmount", value);
          }}
          defaultValue={state.loanAmount}
          error={Boolean(errors.loanAmount)}
          register={register}
          unregister={unregister}
          validation={{
            required: "This is a required field",
            minLength: 1,
            validate: {
              minimumCap: (loanAmount: number) => {
                if (loanAmount < 10000)
                  return "The minimum loan amount allowed is $10,000";
              },
            },
          }}
        />
        <FormErrorMessage name="loanAmount" errors={errors} />
      </div>

      <div className="form-item">
        <StyledLabel htmlFor="reasonForFunds">
          What are you getting financing for?
        </StyledLabel>

        <Select
          options={[
            { value: "expansion", label: "Expansion" },
            {
              value: "equipmentOrVehicle",
              label: "Purchasing Equipment / Vehicle",
            },
            {
              value: "refinancingOrConsolidateDebt",
              label: "Refinancing / Consolidate Debt",
            },
            { value: "renovation", label: "Renovation" },
            { value: "workingCapital", label: "Working capital" },
            { value: "unknown", label: "I Don't Know" },
          ]}
          error={Boolean(errors.reasonForFunds)}
          value={state.reasonForFunds}
          name="reasonForFunds"
          selectProps={{
            ref: register({
              required: "This is a required field",
              minLength: 1,
            }),
          }}
        />

        <FormErrorMessage name="reasonForFunds" errors={errors} />
      </div>

      <Button
        loading={loading}
        onClick={handleSubmit((data) => {
          onSubmit(data);
        })}
        className="submit-button"
      >
        Save and Continue
      </Button>
    </Container>
  );
};

export default StepOne;
