import React, { useEffect } from "react";
import { Container, Input } from "./styles";
import createNumberMask from "text-mask-addons/dist/createNumberMask";

const defaultMaskOptions = {
  prefix: "",
  suffix: "",
  includeThousandsSeparator: true,
  thousandsSeparatorSymbol: ",",
  allowDecimal: true,
  decimalSymbol: ".",
  decimalLimit: 2, // how many digits allowed after the decimal
  allowNegative: false,
  allowLeadingZeroes: false,
};

interface IProps extends React.InputHTMLAttributes<HTMLInputElement> {
  // type any custom props you want to pass other than-
  // existing props for a input like "placeholder", "name" or "type"
  error: boolean;
  register?: any;
  unregister?: any;
  validation?: any;
  onInputChange?: (value: string) => void;
  suffix?: string;
}

const MoneyInput = ({
  error,
  onInputChange = () => {},
  register = () => {},
  unregister = () => {},
  validation = {},
  suffix = "AUD",
  ...inputProps
}: IProps) => {
  const currencyMask = createNumberMask({
    ...defaultMaskOptions,
  });

  useEffect(() => {
    register({ name: inputProps.name }, validation);
  });

  useEffect(() => {
    return () => {
      unregister(inputProps.name);
    };

    // eslint-disable-next-line
  }, []);

  return (
    <Container>
      <p className="text prefix">$</p>
      <Input
        mask={currencyMask}
        error={error}
        className="input"
        {...inputProps}
        onChange={(e: any) => {
          onInputChange(e.target.value.replace("$", "").replace(/,/g, ""));
        }}
      />
      <p className="text suffix">{suffix}</p>
    </Container>
  );
};

export default MoneyInput;
