import React, { useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useResetPasswordMutation } from "../../types/graphql";
import ChangePasswordForm from "../../components/Form/AuthenticationForm/ChangePasswordFrom";
import { LoginContainer, ResetFormContainer } from "./styles";
import { useAlert } from "../../context/AlertProvider";

const ChangePassword = () => {
  const [loading, setLoading] = useState(false);
  const params = useParams();
  const history = useHistory();
  const alert = useAlert();

  const [resetPassword] = useResetPasswordMutation();

  const onSubmit = async (password: string) => {
    setLoading(true);
    try {
      const res = await resetPassword({
        variables: {
          password,
          token: (params as { token: string }).token,
        },
      });
      if (res.data?.resetPassword) {
        alert.showAlert({
          show: true,
          description: "Your password is successfully reset.",
          type: "success",
        });

        history.push("/login");
      } else {
        alert.showAlert({
          show: true,
          description:
            "Something went wrong, maybe your reset link has expired.",
          type: "error",
        });
      }
      setLoading(false);
    } catch (error) {
      console.error("new password", error);
      setLoading(false);
    }
  };

  return (
    <LoginContainer>
      <ResetFormContainer>
        <img
          alt="logo"
          className="logo"
          src={require("../../assets/general/logo.svg")}
        />
        <p className="title">Please enter your new password</p>
        <ChangePasswordForm loading={loading} onSubmit={onSubmit} />
      </ResetFormContainer>
    </LoginContainer>
  );
};

export default ChangePassword;
