import React, { useState } from "react";
import { Container } from "./styles";
import { ImageIcon } from "../Icons/Image";
import { Avatar, useSingleFileUploadMutation } from "../../types/graphql";
import { File } from "../../types/graphql";
import Spinner from "../Spinner";
import { useAlert } from "../../context/AlertProvider";

type Props = {
  setImageType: (
    image: Pick<File, "id" | "name" | "url" | "size"> | undefined
  ) => void;
  defaultImage?: Avatar | null;
  uploadingImage?: (check: boolean) => void;
  uploadImage?: (image?: Pick<File, "id" | "name" | "url" | "size">) => {};
};

const ImageUpload = ({
  defaultImage,
  setImageType,
  uploadingImage,
  uploadImage,
}: Props) => {
  const [image, setImage] = useState({
    preview: "",
    raw: "",
  });

  const [tryUpload, { loading, error }] = useSingleFileUploadMutation();
  const { showAlert } = useAlert();

  const handleChange = async (event: any) => {
    const { target } = event;
    if (target.files.length) {
      setImage({
        preview: URL.createObjectURL(target.files[0]),
        raw: target.files[0],
      });

      const isNotImage =
        target.files[0].type === "image/png" ||
        target.files[0].type === "image/jpeg";

      if (!isNotImage) {
        showAlert({
          type: "error",
          duration: 5000,
          description:
            "Invalid File format. Please make sure the image you upload is in .jpg or .png format",
          show: true,
        });
        return;
      }

      try {
        const response = await tryUpload({
          variables: {
            file: target.files[0],
          },
        });
        if (uploadImage) {
          await uploadImage(response.data?.singleUpload);
        }
        setImageType(response.data?.singleUpload!);
        uploadingImage && uploadingImage(false);
      } catch (e) {
        console.error(e);
        uploadingImage && uploadingImage(false);
      }
    }
  };

  // const handleUpload = async (e: FormEvent) => {
  //   e.preventDefault();
  //   const formData = new FormData();
  //   formData.append("image", image.raw);
  // };

  return (
    <Container error={Boolean(error)}>
      {loading && (
        <div className="spinner-container">
          <Spinner />
        </div>
      )}
      <label htmlFor="upload-button">
        <img
          src={image.preview || defaultImage?.url}
          alt="profile"
          className={`preview-image`}
        />
        {!loading && (
          <div className="upload-button">
            <ImageIcon />
            <span className="button-text">Upload Image</span>
          </div>
        )}
      </label>

      <input
        type="file"
        id="upload-button"
        className="upload-input"
        style={{ display: "none" }}
        onChange={handleChange}
      />
    </Container>
  );
};

export default ImageUpload;
