import React from "react";
import styled from "styled-components";

type Props = {
  size: "small" | "large";
  img?: string;
};

const Image = styled.img<Props>`
  width: ${({ size }) => (size === "large" ? "52px" : "36px")};
  height: ${({ size }) => (size === "large" ? "52px" : "36px")};
  border-radius: 50%;
`;

const Avatar = ({ size, img }: Props) => {
  return <Image src={img} alt="avatar" size={size} />;
};

export default Avatar;
