import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import {
  BorrowerLoanRequestDocument,
  AllLoanRequest,
  LoanRequestStatus,
} from "../../../types/graphql";
import { useAuth } from "../../../context/AuthContext/AuthProvider";
import ErrorCard from "../../../components/ErrorCard";
import { SkeletonCard1 } from "../../../components/Skeleton/SkeletonCard1";
import { SkeletonCard2 } from "../../../components/Skeleton/SkeletonCard2";

import { NoLoanRequest } from "./NoLoanRequest";
import { IncompleteRequest } from "./IncompleteRequest";
import { MatchedOffers } from "./MatchedOffers";
import { NoMatchedOffers } from "./NoMatchedOffers";
import Matching from "./Matching";
import { useApolloClient } from "@apollo/client";

const Home = () => {
  const { user, updateUser } = useAuth();
  const { push } = useHistory();
  const client = useApolloClient();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [loanRequest, setLoanRequest] = useState<AllLoanRequest>();

  useEffect(() => {
    fetchLoanRequest();

    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (user.newUser) {
      push("/loan-request");
      updateUser({ ...user, newUser: false });
      return;
    }
  }, [push, user, updateUser]);

  const fetchLoanRequest = async () => {
    try {
      setError(false);
      const res = await client.query({
        query: BorrowerLoanRequestDocument,
        variables: {
          id: user.id,
        },
      });
      if (res.data) {
        setLoanRequest(res.data.borrowerLoanRequest);
        setLoading(false);

        if (
          res.data.borrowerLoanRequest.status === LoanRequestStatus.Completed
        ) {
          setTimeout(() => {
            fetchLoanRequest();
          }, 3000);
        }
      }
    } catch (error) {
      console.log("error", error);
      setError(true);
    }
  };

  if (loading) {
    return (
      <div>
        <SkeletonCard1 />
        <SkeletonCard2 />
      </div>
    );
  }

  if (error) {
    return (
      <ErrorCard
        title="Error"
        subTitle="Something went wrong please try again."
      />
    );
  }

  /*
    case 1: no loan - step 1
    case 2:loan not completed - step 1: status = 'pending'
    case 3: open for quoting - step 2 - 'when there are less than three offers'
    case 4: closed for quoting - step 2 'there are 3 offers'
    case 5: offer is accepted
  */
  /* // *** V3.5 ***
    case 1: no loan request OR after withdrawal (Archived || Settled)
    case 2: draft (Incomplete)
    case 3: matched offers (Received offer || Selected offer || Not settled)
    case 4: no matched offers (No offer)
    case 5: looking for eligible offers (Matching) 
  */

  const checkAndRenderStep = () => {
    switch (loanRequest?.status) {
      case LoanRequestStatus.Incomplete:
        return (
          <IncompleteRequest loanRequest={loanRequest as AllLoanRequest} />
        );

      case LoanRequestStatus.Completed:
        return <Matching />;

      case LoanRequestStatus.ReceivedOffer:
        return <MatchedOffers loanRequest={loanRequest} />;

      case LoanRequestStatus.NoOffer:
        return <NoMatchedOffers />;

      default:
        return <NoLoanRequest />;
    }
  };
  return checkAndRenderStep();
};

export default Home;
